import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { MatLegacyButtonModule as MatButtonModule } from "@angular/material/legacy-button";
import { MatLegacyFormFieldModule as MatFormFieldModule } from "@angular/material/legacy-form-field";
import { MatIconModule } from "@angular/material/icon";
import { MatLegacyInputModule as MatInputModule } from "@angular/material/legacy-input";
import { BreadCrumbModule, SettingsListModule, LoadingModule } from "@tei/material";
import { ActivitiesAdvancedSettingsComponent } from "./activities-advanced-settings.component";
import { TeiTextTranslatePipeModule } from "@tei/core-translate";
import { RouterModule } from "@angular/router";
import { ActivitiesAdvancedSettingsService } from "../../@services/activities-advanced-settings.service";

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatIconModule, MatButtonModule, MatInputModule, MatFormFieldModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, SettingsListModule, LoadingModule];
const COMPONENTS = [ActivitiesAdvancedSettingsComponent];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    RouterModule.forChild([
      { path: '', component: ActivitiesAdvancedSettingsComponent },
    ]),
  ],
  providers: [{ provide: 'IActivitiesAdvancedSettingsService', useClass: ActivitiesAdvancedSettingsService }]
})

export class ActivitiesAdvancedSettingsModule { }
