import { NoopScrollStrategy } from '@angular/cdk/overlay';
import { LocationStrategy } from '@angular/common';
import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatStepper } from '@angular/material/stepper';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { PostContactSelectionRequest, PostContactSelectionWrapperRequest, WrapperSubmitCallbackArgs } from '@tei/common-contact-lookup-shared';
import { BreadCrumb, SnackBarService } from '@tei/material';
import moment from 'moment';
import { Observable, of, Subscription } from 'rxjs';
import { mergeMap, tap } from 'rxjs/operators';
import { GetPricingResponse } from '../../@intermediate/activity-services/models/get-pricing.model';
import { IAppointmentsService } from '../../@intermediate/appointments/appointments.interface';
import { BookEventAppointmentRequest, BookServiceAppointmentRequest } from '../../@intermediate/appointments/models/post-apponiment-services.model';
import { IActivitiesServicesService } from '../../@intermediate/booking-activities/booking-activities.interface';
import { ActivitiesAllAvailable, ActivitiesEventBookingData, ActivitiesEventDialogModel, ActivitiesFilterQueryParam, ActivitiesServicesDynamicFormModel, ActivitiesServicesFormModel, ActivityTypeEnum, AvailableActivity, AvailableEvent, AvailableService, AvailableSlot, AvailableUnit, Employee, GetActivitiesAvailabilityRequest, GetActivitiesAvailabilityResponse, GetActivitiesAvailableEmployeesAndResourcesResponse, GetActivitiesAvailableServicesRequest, GetActivitiesAvailableServicesResponse, GetContactLookupTemplateResponse, GetEventDatesResponse, GroupInfo, LocalSelectedService, Occurrence, OccurrenceDetail } from '../../@intermediate/booking-activities/models/get-booking-activities.model';
import { AvailabilityServicesRequestForm, PostActivitiesServicesRequestForm } from '../../@intermediate/booking-activities/models/post-booking-activities.model';
import { ActivitiesEventDialogComponent } from '../../components/activities-event-dialog/activities-event-dialog.component';
import { ActivitiesServicesDialogComponent } from '../../components/activities-services-dialog/activities-services-dialog.component';
import { ILocateRouteService } from '@tei/core';
@Component({
  selector: 'tei-booking-activities-services-page',
  templateUrl: './booking-activities-page.component.html',
  styleUrls: ['./booking-activities-page.component.scss']
})
export class BookingActivitiesPageComponent implements OnInit, OnDestroy {
  breadCrumbData: BreadCrumb[] = [];
  allAvailability: GetActivitiesAvailabilityResponse;
  form: PostActivitiesServicesRequestForm = new PostActivitiesServicesRequestForm();
  formModel: ActivitiesServicesFormModel = {
    Time: {
      Label: this.translate.instant('ACTIVITIES.ACTIVITY_SERVICES.TIME')
    },
    Duration: {
      Label: this.translate.instant('ACTIVITIES.ACTIVITY_SERVICES.DURATION'),
      Items: []
    },
    PartySize: {
      Label: this.translate.instant('ACTIVITIES.ACTIVITY_SERVICES.PARTY_SIZE'),
      Items: []
    },
    Employee: {
      Label: this.translate.instant('ACTIVITIES.ACTIVITY_SERVICES.EMPLOYEE'),
      Items: []
    }
  };
  formModelDynamic: ActivitiesServicesDynamicFormModel[] = [];
  contactLookUpConfig: GetContactLookupTemplateResponse;
  availableActivities: AvailableActivity[] = [];
  eventDetails$: Observable<GetEventDatesResponse>;
  GetActivitiesAvailableEmployeesAndResources$: Observable<GetActivitiesAvailableEmployeesAndResourcesResponse>;
  locationID: number;
  findAvailabilityClicked = false;
  locationName: string;
  selectedService = new AvailableService();
  localSelectedService: LocalSelectedService;
  selectedEvent = new AvailableEvent();
  selectedSlot: AvailableSlot;
  selectedEmployee: Employee;
  pricing: GetPricingResponse;
  isLoading: boolean;
  activitiesAllAvailable: ActivitiesAllAvailable = {};
  activitiesEventBookingData: ActivitiesEventBookingData;
  subscription: Subscription;
  availabilityServiceControlNames = Object.keys(new AvailabilityServicesRequestForm().controls);
  showLoadMore = false;
  ActivityTypeEnum = ActivityTypeEnum;
  request: GetActivitiesAvailableServicesRequest;
  requestFilterMapObject: { [key: string]: string; } = {
    CategoryID: "CategoryIDs",
    Type: "ActivityType"
  };

  constructor(
    private translate: TranslateService,
    private snackBar: SnackBarService,
    public dialog: MatDialog,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    @Inject('IActivitiesServicesService') private activitiesServicesService: IActivitiesServicesService,
    @Inject('IAppointmentsService') private appointmentService: IAppointmentsService,
    @Inject('ILocateRouteService') private locateRouteService: ILocateRouteService,
    private activeRoute: ActivatedRoute,
    private locationStrategy: LocationStrategy) {
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  ngOnInit(): void {
    this.setUrlParams(+this.activeRoute.snapshot.paramMap.get('id'), this.activeRoute.snapshot.paramMap.get('name'));
    this.initBreadCrumb();

    this.request = {
      ActivitiesLocationID: this.locationID,
      PageNumber: 1,
    };

    if (this.activeRoute.snapshot?.queryParams["filters"]) {
      this.request = this.getRouteFilters();
      this.setRouteFilters(this.request);
      this.availableActivities = [];
      this.request = {
        ActivitiesLocationID: this.locationID,
        PageNumber: 1,
        ...this.request
      };

      this.loadAvailableActivities();
    } else {
      this.loadAvailableActivities();
    }

    this.availabilityServiceControlNames.forEach(control => {
      this.subscription = this.form.get(control).valueChanges.subscribe(x => {
        if (this.findAvailabilityClicked) {
          this.resetAvailability();
        }
      });

    });
  }

  checkIfImageIsEmpty(imageUrl: string): boolean {
    return (!imageUrl || imageUrl.length === 0);
  }

  onFiltersChange(filters: GetActivitiesAvailableServicesRequest): void {
    this.setRouteFilters(filters);
    this.availableActivities = [];
    this.request = {
      ...filters,
      ActivitiesLocationID: this.locationID,
      PageNumber: 1,
    };
    this.loadAvailableActivities();
  }

  loadAvailableActivities(): void {
    this.isLoading = true;

    this.activitiesServicesService.GetActivitiesAvailableServices(this.request).subscribe(response => {
      this.isLoading = false;

      this.showLoadMore = !this.checkIfLastPage(response);

      this.availableActivities = [...this.availableActivities, ...response.AvailableActivities];
    },
      () => {
        this.isLoading = false;
      });
  }

  private checkIfLastPage(response: GetActivitiesAvailableServicesResponse): boolean {
    const totalLoaded = this.request.PageNumber * response.ServicesPerPage;
    return response.TotalCount <= totalLoaded;
  }

  loadMore() {
    this.request = {
      ...this.request,
      PageNumber: this.request.PageNumber + 1
    };

    this.loadAvailableActivities();
  }

  serviceClick(service: AvailableActivity, stepper: MatStepper): void {
    this.form.patchValue({ Employee: null });
    this.formModel.Time.Start = null;
    this.selectedEvent = null;
    this.isLoading = true;

    if (!this.selectedService) this.selectedService = new AvailableService();
    this.selectedService.ActivityName = service.ActivityName;
    this.selectedService.ActivityDescription = service.ActivityDescription;
    this.selectedService.ActivitiesServiceID = service.ActivityID;
    this.selectedService.ProfilePhotoURL = service.ProfilePhotoURL;
    this.selectedService.Details = service.Details;

    this.resetAvailability();
    this.GetActivitiesAvailableEmployeesAndResources$ = this.activitiesServicesService.GetActivitiesAvailableEmployeesAndResources({
      ActivitiesLocationID: this.locationID,
      ActivitiesServiceID: service.ActivityID
    }).pipe(
      tap((res => {
        this.activitiesAllAvailable.EmployeesAndResources = res;
        this.updateTimePicker(res);
        this.isLoading = false;

        this.localSelectedService = {
          Employees: this.activitiesAllAvailable.EmployeesAndResources.Employees,
          Resources: this.activitiesAllAvailable.EmployeesAndResources.Resources,
          //Service: this.selectedService
          Service: this.updateSelectedService(res)
        };
        this.setFormDynamic(res);
        stepper.next();
      }),
        error => {
          this.isLoading = false;
        }
      )
    );
  }

  updateSelectedService(res: GetActivitiesAvailableEmployeesAndResourcesResponse): AvailableService {
    this.selectedService.ServiceLengthDisplay = res.MetaData?.ServiceLengthDisplay;
    this.selectedService.ServiceLength = res.MetaData?.ServiceLength;
    this.selectedService.LeadInTime = res.MetaData?.LeadInTime;
    this.selectedService.LeadOutTime = res.MetaData?.LeadOutTime;
    this.selectedService.StartTimeIntervalDetails = res.MetaData?.StartTimeIntervalDetails;
    this.selectedService.GroupSize = res.MetaData?.GroupSize;
    this.selectedService.AdditionalTimeMax = res.MetaData?.AdditionalTimeMax;
    this.selectedService.AdditionalTimeInterval = res.MetaData?.AdditionalTimeInterval;

    return this.selectedService;

  }

  showServiceDialog(stepper: MatStepper, slot: AvailableSlot): void {
    this.contactLookUpConfig = null;
    this.selectedSlot = slot;
    this.adjustServiceDialog(this.selectedSlot, stepper);
  }

  eventClick(event: AvailableActivity, stepper: MatStepper): void {
    if (!this.selectedEvent) this.selectedEvent = new AvailableEvent();

    this.selectedEvent.ActivityDescription = event.ActivityDescription;
    this.selectedEvent.ActivityName = event.ActivityName;
    this.selectedEvent.ActivitiesEventID = event.ActivityID;
    this.selectedEvent.ProfilePhotoURL = event.ProfilePhotoURL;
    this.selectedEvent.Details = event.Details;
    this.selectedService = null;
    this.isLoading = true;
    this.eventDetails$ = this.activitiesServicesService.GetEventDates({ ActivityEventID: event.ActivityID })
      .pipe(
        tap((res => {
          this.updateSelectedEvent(res);
          this.isLoading = false;
          stepper.next();

        }),
          error => {
            this.isLoading = false;
          }
        ),
      );
  }
  updateSelectedEvent(res: GetEventDatesResponse) {
    this.selectedEvent.MaxGroupSize = res?.MetaData?.MaxGroupSize;
    this.selectedEvent.ExternalEventID = res?.MetaData?.ExternalEventID;
  }

  showEventDialog(eventOccurrence: Occurrence, stepper: MatStepper): void {
    this.isLoading = true;
    this.activitiesServicesService.GetEventOccurrences({ OccurenceID: +eventOccurrence.EventOccurrenceID }).subscribe(res => {
      this.isLoading = false;
      this.adjustEventDialog(res.EventOccurrances, eventOccurrence, stepper);
    });
  }

  findAvailability(event): void {
    this.findAvailabilityClicked = true;
    this.isLoading = true;
    const serviceLength = this.form.value.Duration;
    const request: GetActivitiesAvailabilityRequest = {
      ActivitiesLocationID: this.locationID,
      ActivitiesServiceID: this.selectedService.ActivitiesServiceID,
      GroupSize: this.form.value.PartySize,
      ServiceLength: serviceLength,
      StartDate: this.startDate,
      NextAvailable: true,
      ExternalEmployeeID: this.form.value.Employee,
    };
    event.ExternalResourceList ? request.ExternalResourceList = event.ExternalResourceList : null;
    this.activitiesServicesService.GetActivitiesAvailability(request).subscribe(
      {
        next: res => {
          this.allAvailability = res;
          this.isLoading = false;
        },
        error: error => {
          this.isLoading = false;
          this.snackBar.showError(error.message);
        }
      });
  }

  onContactLookupSubmitCallback(submitCallbackResult: WrapperSubmitCallbackArgs): void {
    this.postAppointment(submitCallbackResult, submitCallbackResult.Request.ContactSelections);
  }

  private setUrlParams(locationId: number, locationName: string): void {
    this.locationID = locationId;
    this.locationName = locationName;
  }

  private initBreadCrumb(): void {
    this.breadCrumbData = [
      {
        title: this.translate.instant("ACTIVITIES.SETTINGS.LOCATIONS"),
        relativeUrl: '../'
      },
      {
        title: this.locationName,
        relativeUrl: ''
      },
    ];
  }

  private setContactLookUpData(groups: PostContactSelectionWrapperRequest): GroupInfo[] {
    let result: GroupInfo[] = [];
    groups.ContactSelections.forEach((group, index) => {
      const contacts = [];
      group.ContactSelectedResults.forEach((item, index) => {
        contacts.push({
          UserName: item.UserName,
          ContactID: item.ContactID,
          FirstName: item.FirstName,
          LastName: item.LastName,
          Email: item.Email,
          PositionNo: item.PositionNumber,
          IsGuest: item.SelectionType == 2,
          IsHoldTBD: item.SelectionType == 6 || item.ContactID == '88888888-8888-8888-8888-888888888888',
          FieldGroupAnswerIDs: item?.FieldGroupAnswerIDs,
          SendEmailNotification: item.SendNotification
        });
      });

      result.push({
        GroupNo: index + 1,
        Contacts: contacts,
        FieldGroupAnswerIDs: group?.FieldGroupAnswerIDs
      });
    });
    return result;
  }


  private updateTimePicker(availableServicesAndEvents: GetActivitiesAvailableEmployeesAndResourcesResponse): void {
    this.formModel.Time.Start = new Date(moment(availableServicesAndEvents.EarliestTime).format("YYYY-MM-DD[T]HH:mm:ss"));
    this.formModel.Time.End = new Date(moment(availableServicesAndEvents.LatestTime).format("YYYY-MM-DD[T]HH:mm:ss"));
    this.formModel.Time.Interval = 15;
  }

  private adjustServiceDialog(slot: AvailableSlot, stepper: MatStepper): void {
    const activitiesServiceAvailabilityDialogRef = this.dialog.open(ActivitiesServicesDialogComponent, {
      data: {
        buttonLabel: this.translate.instant('ACTIVITIES.ACTIVITY_SERVICES.CONTINUE'),
        service: this.selectedService,
        form: this.form.value,
        timeSlot: slot,
        formModel: this.formModel,
        formModelDynamic: this.formModelDynamic,
        pricing: this.pricing,
        locationID: this.locationID
      },
      maxWidth: '95vw',
      width: '500px',
      panelClass: 'o-dialog-md-fullscreen',
      scrollStrategy: new NoopScrollStrategy(),
    });


    activitiesServiceAvailabilityDialogRef.afterClosed().pipe(
      mergeMap((data) => {
        if (!data) {
          return;
        }
        if (data.selectedEmployee?.ExternalEmployeeID) {
          this.form.get("Employee").setValue(+data.selectedEmployee.ExternalEmployeeID);
        }
        this.pricing = data.pricing;
        return this.activitiesServicesService.GetContactLookupConfig({
          PartySize: this.form.value.PartySize,
          ActivityCode: Number(this.selectedService.ActivitiesServiceID),
          ActivityType: ActivityTypeEnum.Service
        });
      })
    ).subscribe({
      next: res => {
        this.isLoading = false;
        this.contactLookUpConfig = res;
        this.selectedEmployee = this.activitiesAllAvailable?.EmployeesAndResources?.Employees?.find(elem => elem.ExternalId === this.form.value.Employee);
        stepper.next();
      },
      error: error => {
        this.isLoading = false;
      }
    });
  }

  private adjustEventDialog(occurrenceDetails: OccurrenceDetail[], eventOccurrence: Occurrence, stepper: MatStepper): void {

    const activitiesEventDialogRef = this.dialog.open(ActivitiesEventDialogComponent, {
      data: {
        buttonLabel: this.translate.instant('ACTIVITIES.ACTIVITY_SERVICES.CONTINUE'),
        event: this.selectedEvent,
        eventOccurrence: eventOccurrence,
        occurrenceDetails: occurrenceDetails,
        locationId: this.locationID,
        location: this.locationName
      },
      maxWidth: '95vw',
      width: '500px',
      panelClass: 'o-dialog-md-fullscreen',
      scrollStrategy: new NoopScrollStrategy(),
    });

    activitiesEventDialogRef.afterClosed().pipe(
      mergeMap((data: ActivitiesEventDialogModel) => {
        if (data) {
          this.activitiesEventBookingData = {
            numberOfPlayer: data.numberOfRegistrant,
            total: data.total,
            tax: data.tax,
            subTotal: data.subTotal,
            occurrenceDetails: occurrenceDetails
          };
          return this.activitiesServicesService.GetContactLookupConfig(
            {
              PartySize: data.numberOfRegistrant,
              ActivityCode: this.selectedEvent.ActivitiesEventID,
              ActivityType: ActivityTypeEnum.Event
            }
          );
        } else {
          return of(null);
        }
      })).subscribe(res => {
        this.isLoading = false;
        if (res) {
          this.contactLookUpConfig = null;
          setTimeout(() => {
            this.contactLookUpConfig = res;
            this.selectedEmployee = this.activitiesAllAvailable?.EmployeesAndResources?.Employees?.find(elem => elem.ExternalId === this.form.value.Employee);
            stepper.next();
          }, 500);
        }
      });
  }

  private postAppointment(submitCallbackResult: WrapperSubmitCallbackArgs, contactSelections: PostContactSelectionRequest[]): void {
    this.isLoading = true;
    if (this.selectedService) {
      this.bookServiceAppointment(submitCallbackResult, contactSelections);
    }
    else if (this.selectedEvent) {
      this.bookEventAppointment(submitCallbackResult, contactSelections);
    }
    this.isLoading = false;
  }

  private getAllResourceUnits(): AvailableUnit[] {
    const resourceUnits = [];

    this.activitiesAllAvailable?.EmployeesAndResources?.Resources?.map(resource => {
      resource.Units.map(unit => {
        resourceUnits.push(unit);
      });
    });

    return resourceUnits;
  }

  private bookServiceAppointment(submitCallbackResult: WrapperSubmitCallbackArgs, contactSelections: PostContactSelectionRequest[]): void {
    let allUnits = this.getAllResourceUnits();

    let externalEmployeeID = this.selectedSlot.ExternalEmployee && this.selectedSlot.ExternalEmployee.length > 0 ?
      +this.selectedSlot.ExternalEmployee
        .find(p => +p.ExternalEmployeeID === this.form.value.Employee).ExternalEmployeeID : null;

    let request: BookServiceAppointmentRequest = {
      ExternalEmployeeID: externalEmployeeID,
      ActivitiesLocationID: this.locationID,
      ContactLookupSelectionGroupUID: submitCallbackResult.Response.SelectionGroupUID,
      Notes: this.getNotes(submitCallbackResult.Request),
      ActivitiesServiceID: this.selectedService.ActivitiesServiceID,
      StartTime: this.selectedSlot.StartTime,
      GroupSize: this.form.value.PartySize,
      Groups: this.setContactLookUpData(submitCallbackResult.Request),
      ContactIDList: this.contactLookUp(contactSelections),
      ExternalResourceUnitIDList: this.selectedSlot.ExternalResourceUnitIDList,
      ServiceLength: this.form.value.Duration,
      EndTime: null,
      ExternalEmployeeName: this.selectedEmployee?.EmployeeName,
      ExternalResourceUnits: this.selectedSlot.ExternalResourceUnitIDList.map((val, i) => (
        {
          ID: val,
          Label: allUnits.find(unit => unit.ExternalId == val)?.Description ?? null,
          Name: allUnits.find(unit => unit.ExternalId == val)?.NickName ?? null
        }
      )).filter(eru => eru.ID)
    };

    this.appointmentService.BookServiceAppointment(request).subscribe(
      (response) => {
        this.isLoading = false;
        if (response.IsPrePay) {

          this.locateRouteService.GetDefaultPage({
            ApplicationIdentifier: "web"
          }).subscribe(res => {
            localStorage.setItem("PaymentEngineReturnURL", res.DefaultPageUrl)
            this.router.navigate(['/payment-engine/checkout/']);
          })
        } else {
          this.router.navigate([`../../../confirmation/${response.AppointmentID}/${this.locationID}/${this.locationName}`], { relativeTo: this.activatedRoute });
        }
      },
      (err) => {
        this.isLoading = false;
      });
  }

  private bookEventAppointment(submitCallbackResult: WrapperSubmitCallbackArgs, contactSelections: PostContactSelectionRequest[]): void {
    let allUnits = this.getAllResourceUnits();

    let request: BookEventAppointmentRequest = {
      ActivitiesLocationID: this.locationID,
      ActivitiesEventID: this.selectedEvent.ActivitiesEventID,
      ContactLookupSelectionGroupUID: submitCallbackResult.Response.SelectionGroupUID,
      Notes: this.getNotes(submitCallbackResult.Request),
      GroupSize: this.activitiesEventBookingData.numberOfPlayer,
      Groups: this.setContactLookUpData(submitCallbackResult.Request),
      StartTime: this.activitiesEventBookingData?.occurrenceDetails[0].StartDate,
      ExternalEventOccurrenceID: this.activitiesEventBookingData?.occurrenceDetails[0]?.EventOccurrenceID,
      ContactIDList: this.contactLookUp(contactSelections),
      EndTime: this.activitiesEventBookingData?.occurrenceDetails[0].EndDate,
    };
    this.appointmentService.BookEventAppointment(request).subscribe(
      (response) => {
        this.isLoading = false;
        if (response.IsPrePay) {
          this.locateRouteService.GetDefaultPage({
            ApplicationIdentifier: "web"
          }).subscribe(res => {
            localStorage.setItem("PaymentEngineReturnURL", res.DefaultPageUrl)
            this.router.navigate(['/payment-engine/checkout/']);
          })
        } else {
          this.router.navigate([`../../../confirmation/${response.AppointmentID}/${this.locationID}/${this.locationName}`], { relativeTo: this.activatedRoute });
        }
      },
      (err) => {
        this.isLoading = false;
        this.snackBar.showError(err.error?.Title);
      });
  }

  private resetAvailability(): void {
    if (this.allAvailability?.AvailabilityList) {
      this.allAvailability.AvailabilityList = [];
      this.allAvailability = JSON.parse(JSON.stringify(this.allAvailability));
    }
    this.findAvailabilityClicked = false;
  }

  private contactLookUp(contactList: PostContactSelectionRequest[]): string[] {
    const contacts = [];
    contactList.forEach(group => {
      group.ContactSelectedResults.forEach((item) => {
        contacts.push(item.ContactID);
      });
    });

    return contacts;
  }

  private setFormDynamic(res: GetActivitiesAvailableEmployeesAndResourcesResponse): void {
    this.formModelDynamic = [];
    res.Resources.forEach((item, index) => {
      let currentIndex = this.formModelDynamic.push(
        {
          Name: item.Description.replace(/ /g, "_"),
          Id: item.ExternalId,
          Label: item.Description,
          Items: []
        });

      if (item.AllowAnyAvailable) {
        this.formModelDynamic[currentIndex - 1].Items.push({
          label: this.translate.instant("ACTIVITIES.ACTIVITY_SERVICES.ANY_AVAILABLE"),
          value: item.Units[0].ExternalId
        });
      }

      if (!item.ShowUnits) {
        if (!item.IsRequired) {
          this.formModelDynamic[currentIndex - 1].Items.push({
            label: this.translate.instant("ACTIVITIES.ACTIVITY_SERVICES.NO"),
            value: 'No'
          });
        }

        this.formModelDynamic[currentIndex - 1].Items.push({
          label: this.translate.instant("ACTIVITIES.ACTIVITY_SERVICES.YES"),
          value: 'Yes'
        });
      }
      else if (item.ShowUnits) {
        if (!item.IsRequired) {
          this.formModelDynamic[currentIndex - 1].Items.push({
            label: this.translate.instant("ACTIVITIES.ACTIVITY_SERVICES.NONE"),
            value: 'None'
          });
        }

        item.Units.forEach((unit, i) => {
          this.formModelDynamic[currentIndex - 1].Items.push({
            label: `${unit.Description}`,
            value: unit.ExternalId
          });
        });
      }
    });

    this.formModelDynamic = [...this.formModelDynamic];
  }

  private get startDate() {
    return moment(this.form.value.Date).format("YYYY-MM-DD") + moment(this.form.value.Time).format("[T]HH:mm:ss");
  }

  getNotes(groups: PostContactSelectionWrapperRequest): string {
    const notes: string[] = [];

    groups?.ContactSelections?.forEach(contact => {
      contact?.ContactSelectedResults?.forEach(fieldGroup => {
        fieldGroup?.FieldGroupLookupWrapper?.FieldGroupLookups?.forEach(fieldGroupLookup => {
          fieldGroupLookup?.MasterFieldAnswers?.forEach(answer => {
            this.processAnswer(answer, notes);
          });
        });
      });

      contact?.FieldGroupLookupWrapper?.FieldGroupLookups?.forEach(fieldGroup => {
        fieldGroup?.MasterFieldAnswers?.forEach(answer => {
          this.processAnswer(answer, notes);
        });
      });
    });

    return notes.join('   ');
  }

  private processAnswer(answer: any, noteArr: string[]): void {
    if (answer.Answer) {
      noteArr.push(`Q: '${answer?.MasterFieldLabel}' A: '${answer.Answer}'`);
    }

    if (answer.MasterFieldAnswerOptions) {
      answer?.MasterFieldAnswerOptions?.forEach(option => {
        noteArr.push(`Q: '${answer.MasterFieldLabel}' A: '${option.Value}'`);
      });
    }
  }

  getRouteFilters(): GetActivitiesAvailableServicesRequest {
    let filters = new GetActivitiesAvailableServicesRequest();
    filters.ActivitiesLocationID = this.locationID;
    filters.PageNumber = 1;

    if (this.activeRoute.snapshot.queryParams["filters"]) {
      const routeFilters: ActivitiesFilterQueryParam = JSON.parse(this.activeRoute.snapshot.queryParams["filters"]);

      Object.keys(routeFilters).forEach(key => {
        const newKey = this.requestFilterMapObject[key] || key;
        filters[newKey] = routeFilters[key];
      });

      if (routeFilters.CategoryID && routeFilters.CategoryID.length > 0) {
        filters.CategoryIDs = routeFilters.CategoryID[0];
      }

      if (routeFilters.Type && routeFilters.Type.length > 0) {
        filters.ActivityType = routeFilters.Type[0];
      }

      const hasFilter = Object.values(filters).some(value => value);

      const queryParams = hasFilter ? JSON.stringify(filters) : null;
      let path = this.router.url.split("?")[0];
      this.locationStrategy.pushState(null, '', path, queryParams ? "?filters=" + queryParams : "");
    }

    return filters;
  }

  setRouteFilters(filters: GetActivitiesAvailableServicesRequest): void {
    const queryFilters: ActivitiesFilterQueryParam = Object.entries(this.requestFilterMapObject)
      .reduce((acc, [key, val]) => {
        if (filters[val]) {
          if (typeof (filters[val]) === "object") {
            acc[key] = filters[val];
          } else {
            acc[key] = [filters[val]];
          }
        }
        return acc;
      }, {} as ActivitiesFilterQueryParam);

    const hasFilter = Object.values(queryFilters).some(value => value);

    const queryParams = hasFilter ? JSON.stringify(queryFilters) : null;
    let path = this.router.url.split("?")[0];
    this.locationStrategy.pushState(null, '', path, queryParams ? "?filters=" + queryParams : "");
  }
}
