import { Component, EventEmitter, Input, Output } from '@angular/core';
import { AvailableActivity } from '../../@intermediate/booking-activities/models/get-booking-activities.model';

@Component({
  selector: 'tei-activities-service',
  templateUrl: './activities-service.component.html',
  styleUrls: ['./activities-service.component.scss']
})
export class ActivitiesServiceComponent
{
  @Input() service: AvailableActivity;
  @Input() fullHeight: boolean = true;
  @Input() stepIndex: number;
  @Output() serviceClick = new EventEmitter<AvailableActivity | null>(null);
  constructor() { }

  onClickService(service: AvailableActivity)
  {
    this.serviceClick.emit(service);
  }
}
