import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IActivitiesAdvancedSettingsService } from '../@intermediate/activities-advanced-settings/activities-advanced-settings.interface';
import { GetActivitiesAdvancedSettingsResponse } from '../@intermediate/activities-advanced-settings/models/get-activities-advanced-settings.model';
import { PosActivitiesAdvancedSettingsResponse, PostActivitiesAdvancedSettingsRequest } from '../@intermediate/activities-advanced-settings/models/post-activities-advanced-settings.model';
import { GetActivitiesEventsSettingsRequest } from '../@intermediate/activities-events-settings/models/get-events.model';

@Injectable({
  providedIn: 'root'
})

export class ActivitiesAdvancedSettingsService implements IActivitiesAdvancedSettingsService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/Booking/Activities/AdvancedSettings";
  }

  Get(request: GetActivitiesEventsSettingsRequest): Observable<GetActivitiesAdvancedSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/UScheduleAdvancedSettings';
    return this.http.get<GetActivitiesAdvancedSettingsResponse>(apiUrl, { params: request as any });
  }

  Post(request: PostActivitiesAdvancedSettingsRequest): Observable<PosActivitiesAdvancedSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/UScheduleAdvancedSettings';
    return this.http.post<PosActivitiesAdvancedSettingsResponse>(apiUrl, request);
  }
}
