import { Component, EventEmitter, Inject, Injectable, Input, OnInit, Optional, Output } from '@angular/core';
import { UntypedFormArray, UntypedFormControl } from '@angular/forms';
import { MAT_MOMENT_DATE_ADAPTER_OPTIONS, MomentDateAdapter } from '@angular/material-moment-adapter';
import { DateAdapter, MAT_DATE_LOCALE } from '@angular/material/core';
import { MatDatepicker } from '@angular/material/datepicker';
import { ActivitiesServicesDynamicFormModel, ActivitiesServicesFormModel, AvailableService, AvailableSlot, Employee, LocalSelectedService, Resource, SelectedResourceUnit } from '../../@intermediate/booking-activities/models/get-booking-activities.model';
import { PostActivitiesServicesRequestForm } from '../../@intermediate/booking-activities/models/post-booking-activities.model';

@Injectable()
export class MyDateAdapter extends MomentDateAdapter
{
  constructor(
    @Inject(MAT_DATE_LOCALE) locale: string,
    @Optional() @Inject(MAT_MOMENT_DATE_ADAPTER_OPTIONS) private options?: any
  )
  {
    super(locale, options);
  }

  format(date: moment.Moment, displayFormat: string): string
  {
    if (this.locale.includes("fr"))
    {
      return date.format('ddd, MMM D, YYYY');
    } else
    {
      return date.format('ddd, MMM D, YYYY');
    }
  }

  setDateFormat(format: string)
  {
    this.setLocale(this.locale);

    this.setDateFormat(format);
  }
}

@Component({
  selector: 'tei-activities-activities-availability',
  templateUrl: './activities-availability.component.html',
  styleUrls: ['./activities-availability.component.scss'],
  providers: [
    {
      provide: DateAdapter,
      useClass: MyDateAdapter,
    },
    {
      provide: MAT_DATE_LOCALE, useValue: JSON.parse(localStorage.getItem('profile-settings')) ?
        JSON.parse(localStorage.getItem('profile-settings'))?.language : 'en'
    }
  ]

})
export class ActivitiesAvailabilityComponent implements OnInit
{

  minDate = new Date();
  @Input() form: PostActivitiesServicesRequestForm;
  @Input() allAvailability: AvailableSlot[];
  @Input() formModel: ActivitiesServicesFormModel;
  _formModelDynamic: ActivitiesServicesDynamicFormModel[];
  get formModelDynamic(): ActivitiesServicesDynamicFormModel[]
  {
    return this._formModelDynamic;
  }
  @Input() set formModelDynamic(value: ActivitiesServicesDynamicFormModel[])
  {
    this._formModelDynamic = value;
  }
  @Input() loading: boolean = false;
  @Output() availableHourSelected: EventEmitter<AvailableSlot> = new EventEmitter<AvailableSlot>();
  @Output() findTheAvailability: EventEmitter<object> = new EventEmitter<object>();
  //TODO this should be removed
  @Input() findAvailabilityClicked: boolean;
  @Input() selectedService: LocalSelectedService;
  nextAvailableSlots: AvailableSlot[];
  SelectedResourceUnit = SelectedResourceUnit;

  constructor() { }
  ngOnInit()
  {
    this.form.controls["Time"].setValue(this.formModel.Time.Start);
    this.InitializeComponent(this.selectedService?.Service, this.selectedService?.Employees, this.selectedService?.Resources);
    this.setFormDynamicFields(this.formModelDynamic);
  }

  InitializeComponent(AvailableService: AvailableService, Employees: Employee[], Resources: Resource[])
  {
    this.formModel.Duration.Items = [];
    this.formModel.PartySize.Items = [];
    this.formModel.Employee.Items = [];

    this.fillDuration(AvailableService.AdditionalTimeInterval, AvailableService.ServiceLength, AvailableService.AdditionalTimeMax);

    this.fillPartySize(AvailableService.GroupSize);


    if (Employees?.length)
    {
      Employees.forEach((item) =>
      {
        this.formModel.Employee.Items.push({
          label: `${ item.EmployeeName }`,
          value: item.ExternalId
        });
      });
    }

    if (this.formModel.Employee.Items && this.formModel.Employee.Items[0])
    {
      this.form.patchValue({ Employee: this.formModel.Employee.Items[0].value });
    }

  }

  private fillPartySize(GroupSize: number)
  {
    this.form.patchValue({ PartySize: 1 });
    for (let i = 1; i <= GroupSize; i++)
    {
      this.formModel.PartySize.Items.push({
        label: `${ i }`,
        value: i
      });
    }
  }

  private fillDuration(AdditionalTimeInterval: number, ServiceDuration: number, AdditionalTimeMax: number)
  {
    const interval = AdditionalTimeInterval;
    const serviceLength = ServiceDuration;
    this.form.patchValue({ Duration: serviceLength });

    if(AdditionalTimeMax == 0 && AdditionalTimeInterval == 0)
    {
      this.formModel.Duration.Items.push({
        label: `${ serviceLength } Minutes`,
        value: serviceLength
      });
    }
    else
    {
      for (let i = serviceLength; i <= AdditionalTimeMax; i += interval ? interval : serviceLength)
      {
        this.formModel.Duration.Items.push({
          label: `${ i } Minutes`,
          value: i
        });
      }
    }
  }

  setFormDynamicFields(activitiesServicesDynamicFormModel: ActivitiesServicesDynamicFormModel[])
  {
    this.clearFormArray(this.getFormArrayByName('DynamicFields'));
    activitiesServicesDynamicFormModel.forEach((item) =>
    {
      this.getFormArrayByName('DynamicFields').push(new UntypedFormControl(item.Items[0].value));
    });
  }

  clearFormArray(formArray: UntypedFormArray)
  {
    while (formArray.length !== 0)
    {
      formArray.removeAt(0);
    }
  }

  openDatePicker(picker: MatDatepicker<any>)
  {
    picker.open();
  }

  findAvailability()
  {
    this.findTheAvailability.emit(this.getExternalResourceID(0));
  }

  getFormControlByName(name: string): UntypedFormControl
  {
    return this.form.get(name) as UntypedFormControl;
  }

  getFormArrayByName(name: string): UntypedFormArray
  {
    return this.form.get(name) as UntypedFormArray;
  }

  onTimeSelected(timeSlot: AvailableSlot): void
  {
    this.availableHourSelected.emit(timeSlot);
  }

  isDateGreaterThanToday(): boolean
  {
    const today = new Date();
    return this.form.get('Date').value > today;
  }

  get distinctDate(): Date[]
  {
    return this.form.get('Date').value;
  }

  get getDistinctDates()
  {
    if (this.allAvailability)
    {
      const datesSet = new Set<string>();

      this.allAvailability.forEach(availableItem =>
      {
        const startDate = new Date(availableItem.StartTime).toLocaleDateString();
        datesSet.add(startDate);
      });

      return Array.from(datesSet);
    }
    else
    {
      return [];
    }
  }

  filterAvailabilityByDate(selectedDate: string): AvailableSlot[]
  {
    const filteredAvailability = this.allAvailability?.filter(availableItem =>
    {
      const startDate = new Date(availableItem.StartTime).toLocaleDateString();
      return startDate === selectedDate;
    }).sort(this.compareSlotsByStartTime);

    return filteredAvailability;
  }

  get HasNextAvailability()
  {
    return this.allAvailability?.filter(availableItem =>
    {
      const startDate = new Date(availableItem.StartTime).toLocaleDateString();
      return startDate === this.getDistinctDates[1];
    }).length > 0;
  }

  get getTheNextAvailableSlots()
  {
    return this.allAvailability?.filter(availableItem =>
    {
      const startDate = new Date(availableItem.StartTime).toLocaleDateString();
      return startDate === this.getDistinctDates[1];
    }).sort(this.compareSlotsByStartTime);
  }

  compareSlotsByStartTime(slotA: AvailableSlot, slotB: AvailableSlot)
  {
    return new Date(slotA.StartTime).getTime() - new Date(slotB.StartTime).getTime();
  }

  getExternalResourceID(index = 0): object
  {
    const selectedResourceUnit = this.form.value.DynamicFields[index];
    let externalResourceID = [];
    let ExternalResourceUnitID = [];
    let ExternalResourceList: any = [];
    this._formModelDynamic.forEach(item =>
    {
      externalResourceID.push(item.Id);
    });

    this.form.value.DynamicFields.forEach(item =>
    {
      ExternalResourceUnitID.push(item);
    });



    ExternalResourceUnitID.forEach((item, index) =>
    {
      let ResourceUnitID = (selectedResourceUnit == SelectedResourceUnit.NONE || selectedResourceUnit == SelectedResourceUnit.ANY || selectedResourceUnit == SelectedResourceUnit.YES || selectedResourceUnit == SelectedResourceUnit.NO) ? item.toString().toUpperCase() : item.toString();
      ExternalResourceList.push(
        {
          ExternalResourceID: externalResourceID[index].toString(),
          ExternalResourceUnitID: ResourceUnitID
        });
    });

    return {
      ExternalResourceList: ExternalResourceList
    };
  }
}
