import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, ConfirmationDialogComponent, SnackBarService } from '@tei/material';
import { filter, switchMap } from 'rxjs/operators';
import { ActivityService, GetActivityServicesRequest, GetActivityServicesResponse } from '../../@intermediate/activity-services';
import { IActivityServicesService } from '../../@intermediate/activity-services/activity-services.interface';
import { PostActivityServicesRequestForm } from '../../@intermediate/activity-services/models/post-activity-services.model';


@Component({
  selector: 'tei-activities-pages-activity-services',
  templateUrl: './activity-services.component.html',
  styleUrls: ['./activity-services.component.scss']
})
export class ActivityServicesComponent implements OnInit
{
  model: GetActivityServicesResponse;
  form: PostActivityServicesRequestForm;

  dataSource = new MatTableDataSource<ActivityService>();
  isLoading = false;
  displayedColumns: string[] = ['ExternalServiceID', 'ServiceName', 'Locations', 'Categories', 'Status', 'Actions', 'MobileMode'];
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.ACTIVITIES"),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.SERVICES"),
      relativeUrl: ''
    },
  ];
  constructor(
    public dialog: MatDialog,
    private snackBar: SnackBarService,
    private translate: TranslateService,
    @Inject('IActivityServicesService') private ActivityServicesService: IActivityServicesService
  )
  {

  }

  ngOnInit() { }

  private load(request: Partial<GetActivityServicesRequest>)
  {
    if (!this.model)
    {
      this.model = new GetActivityServicesResponse();
    }
    this.form = new PostActivityServicesRequestForm();
    this.ActivityServicesService.Get(request).subscribe(response =>
    {

      this.setResponseToForm(response);
    });
  }

  onFiltersChange(filters: GetActivityServicesRequest): void
  {
    this.load(filters);
  }

  private setResponseToForm(response: GetActivityServicesResponse)
  {
    this.model = response;
    this.form.setValue(response);
    this.dataSource = new MatTableDataSource<ActivityService>(this.addLocationsByCommaSeparated(this.model.ActivityServices));
  }

  addLocationsByCommaSeparated(activityServices: ActivityService[]): ActivityService[]
  {
    return activityServices.map(activityService => (
      {
        ...activityService,
        Locations: activityService.ActivityLocations.map(al => al.LocationName)?.join(', ')
      }
    ));
  }

  deleteLocation(item: ActivityService)
  {
    const dialogData = {
      dialogData: {
        title: 'ACTIVITIES.ACTIVITY_SERVICES.DELETE_DIALOG.HEADER',
        content: `ACTIVITIES.ACTIVITY_SERVICES.DELETE_DIALOG.MESSAGE`,
        submitButtonLabel: 'ACTIVITIES.WORDS.DELETE',
        submitButtonColor: 'warn'
      }
    };

    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      maxWidth: '18vw',
      minWidth: '320px',
      panelClass: 'o-dialog-change-preferences',
      data: dialogData
    });

    dialogRef.afterClosed().pipe(
      filter(result => result),
      switchMap(
        result => this.ActivityServicesService.PostDeactivate({
          ExternalCode: item.ExternalServiceID,
          ServiceID: item.ActivityServiceID,
          ServiceName: item.ServiceName
        })),
      switchMap(_ => this.ActivityServicesService.Get({}))
    ).subscribe(response =>
    {
      this.model = response;
      this.dataSource = new MatTableDataSource<ActivityService>(this.addLocationsByCommaSeparated(this.model.ActivityServices));
      this.snackBar.showSuccess('Deactivated!');
    });

  }

  save(form: PostActivityServicesRequestForm)
  {
    this.ActivityServicesService.Post(form.value).subscribe(response =>
    {

    });
  }
}
