import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, ChipItemModel, ConfirmationDialogComponent, SnackBarService } from '@tei/material';
import { IActivitiesEventsSettingsService } from '../../@intermediate/activities-events-settings/activities-events-settings.interface';
import { DeleteEventRequest } from '../../@intermediate/activities-events-settings/models/delete-event.model';
import { ActivityEvent, GetActivitiesEventsSettingsRequest, Location } from '../../@intermediate/activities-events-settings/models/get-events.model';

@Component({
  selector: 'tei-booking-activities-activities-events-settings',
  templateUrl: './activities-events-settings.component.html',
  styleUrls: ['./activities-events-settings.component.scss']
})
export class ActivitiesEventsSettingsComponent implements OnInit
{
  dataSource = new MatTableDataSource<ActivityEvent>();
  displayedColumns: string[] = ['EventID', 'EventName', 'Locations', 'Categories', 'Status', 'Actions', 'MobileMode'];
  tempImage: any = '/assets/images/tei-logo.png';
  isLoading = false;
  locations: Location[] = [];
  constructor(
    public dialog: MatDialog,
    private snackBar: SnackBarService,
    private translate: TranslateService,
    private activeRoute: ActivatedRoute,
    @Inject("IActivitiesEventsSettingsService") private eventsSettingsService: IActivitiesEventsSettingsService,
    private router: Router
  ) { }

  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.ACTIVITIES"),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.EVENTS"),
      relativeUrl: ''
    },
  ];

  ngOnInit(): void
  {
    const request: GetActivitiesEventsSettingsRequest = {
      IsActive: true,
    };
    this.loadData(request);
  }

  openDeleteEventDialog(activityEventID: number)
  {
    console.log(activityEventID);
    const dialogData = {
      dialogData: {
        title: 'Delete Event',
        content: `Are you sure you want to delete this Event?`,
        submitButtonLabel: 'Delete',
        submitButtonColor: 'warn'
      }
    };

    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      maxWidth: '18vw',
      minWidth: '320px',
      panelClass: 'o-dialog-change-preferences',
      data: dialogData
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.deleteEvent(activityEventID);
      }

    });
  }

  deleteEvent(activityEventID: number)
  {
    let request: DeleteEventRequest = {
      ActivityEventID: activityEventID
    };
    this.isLoading = true;
    this.eventsSettingsService.Delete(request).subscribe(
      () =>
      {
        this.isLoading = false;
        this.snackBar.showSuccess("Event deleted successfully.");
        this.loadData(new GetActivitiesEventsSettingsRequest());
      },
      (err) =>
      {
        this.isLoading = false;
      });
  }

  loadData(request: GetActivitiesEventsSettingsRequest): void
  {
    this.isLoading = true;
    this.eventsSettingsService.Get(request).subscribe(
      (result) =>
      {
        this.isLoading = false;
        this.dataSource = new MatTableDataSource<ActivityEvent>(this.addLocationsByCommaSeparated(result.Events));
        if (!this.locations.length)
        {
          this.locations = result.AllActivityLocations;
        }
      },
      (err) =>
      {
        this.isLoading = false;
        this.snackBar.showError(err.message);
      });

  }

  addLocationsByCommaSeparated(activityServices: ActivityEvent[]): ActivityEvent[]
  {
    return activityServices.map(activityService => (
      {
        ...activityService,
        Locations: activityService.ActivityLocations.map(al => al.LocationName)?.join(', ')
      }
    ));
  }


  navigateToManageEvent(eventID?: number): void
  {
    this.router.navigate(['manage-event', eventID], { relativeTo: this.activeRoute });
  }

  onStatusFilterChange(filterObjects: Array<ChipItemModel>): void
  {
    let chipItem = filterObjects[0];
    let request = new GetActivitiesEventsSettingsRequest();
    if (chipItem.value != undefined)
    {
      request.IsActive = chipItem.value !== 'Inactive';
    }
    else
    {
      delete request.IsActive;
    }
    this.loadData(request);
  }

  onFiltersChange(filters: GetActivitiesEventsSettingsRequest): void
  {
    this.loadData(filters);
  }
}

