import { Component, EventEmitter, Input, Output } from '@angular/core';
import { AvailableActivity } from '../../@intermediate/booking-activities/models/get-booking-activities.model';

@Component({
  selector: 'tei-activities-event',
  templateUrl: './activities-event.component.html',
  styleUrls: ['./activities-event.component.scss']
})
export class ActivitiesEventComponent
{
  @Input() event: AvailableActivity;
  @Input() stepIndex: number;
  @Input() fullHeight: boolean = true;
  @Output() eventClick = new EventEmitter<AvailableActivity | null>(null);

  onClickEvent(event: AvailableActivity)
  {
    this.eventClick.emit(event);
  }
}
