import { Location } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, CardDialogComponent, ConfirmationDialogComponent } from '@tei/material';
import { firstValueFrom } from 'rxjs';
import { IAppointmentsService } from '../../@intermediate/appointments/appointments.interface';
import { BookingDetail, GetAppointmentBookingDetailRequest } from '../../@intermediate/appointments/models/get-appointment-booking-detail.model';
import { PostCancelAppointmentRequest } from '../../@intermediate/appointments/models/post-cancel-appointment.model';
@Component({
  selector: 'tei-activities-reservation-details',
  templateUrl: './reservation-details.component.html',
  styleUrls: ['./reservation-details.component.scss']
})
export class ReservationDetailsComponent implements OnInit
{
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant("ACTIVITIES.DETAILS.ITINERARY"),
      relativeUrl: '/web/my-account/itinerary'
    },
    {
      title: this.translate.instant("ACTIVITIES.DETAILS.MANAGE"),
      relativeUrl: ''
    }
  ];;
  model: BookingDetail;
  defaultPicture: string;
  appointmentID: number;

  constructor(
    private translate: TranslateService,
    public dialog: MatDialog,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    @Inject('IAppointmentsService') private appointmentService: IAppointmentsService,
    private location: Location
  )
  {
  }

  ngOnInit(): void
  {
    this.appointmentID = this.activatedRoute.snapshot.params['appointmentID'];
    this.load(this.appointmentID);
  }

  load(appointmentID: number): void
  {
    const request: GetAppointmentBookingDetailRequest =
    {
      AppointmentID: appointmentID
    };
    this.appointmentService.GetBookingDetail(request).subscribe(response =>
    {
      this.model = response?.BookingDetails[0];
      this.defaultPicture = this.model?.ServiceProfilePhotoURL ?? this.model?.EventProfilePhotoURL ?? '/assets/images/tei-logo.png';
    });
  }

  async cancel(): Promise<void>
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'ACTIVITIES.DETAILS.CANCEL_CONFIRM_DIALOG_TITLE',
          content: 'ACTIVITIES.DETAILS.CANCEL_CONFIRM_DIALOG_MESSAGE',
          submitButtonLabel: 'ACTIVITIES.DETAILS.CONFIRM',
          submitButtonColor: 'warn'
        }
      }
    });

    try
    {
      const result = await firstValueFrom(dialogRef.afterClosed());

      if (result)
      {
        const request: PostCancelAppointmentRequest =
        {
          ReferenceID: this.model.ExternalCode,
          AppointmentID: this.model.AppointmentID
        };
        await firstValueFrom(this.appointmentService.CancelAppointment(request));

        this.location.back();
      }
    } catch (error)
    {
    }
  }
}
