import { DatePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BreadCrumb } from '@tei/material';
import { IAppointmentsService } from '../../@intermediate/appointments/appointments.interface';
import { ActivitiesAppointment, GetActivitiesAppointmentRequest } from '../../@intermediate/booking-activities/models/get-booking-activities.model';

@Component({
  selector: 'tei-booking-activities-confirmation',
  templateUrl: './confirmation.component.html',
  styleUrls: ['./confirmation.component.css']
})
export class ConfirmationComponent implements OnInit
{
  model: ActivitiesAppointment;
  constructor(
    @Inject('IAppointmentsService') private appointmentService: IAppointmentsService,
    private route: ActivatedRoute,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private datePipe: DatePipe
  )
  {
  }

  confirmationNumber: number;
  LocationID: number;
  LocationName: string;
  defaultPicture: string;
  breadCrumbData: BreadCrumb[];
  startDate: string;
  ngOnInit(): void
  {
    this.confirmationNumber = +this.route.snapshot.paramMap.get('ConfirmationNumber');
    this.LocationID = +this.route.snapshot.paramMap.get('LocationID');
    this.LocationName = this.route.snapshot.paramMap.get('LocationName');
    this.load(this.confirmationNumber);
    this.breadCrumbData = [
      {
        title: 'Locations',
        relativeUrl: '../../../../'
      },
      {
        title: this.LocationName,
        relativeUrl: `../../../../activities-services/${ this.LocationID }/${ this.LocationName }`
      },
      {
        title: 'Confirmation',
        relativeUrl: ''
      }
    ];
  }

  formatDate(inputDate: Date): string
  {
    const formattedDate = this.datePipe.transform(inputDate, 'EEE, MMM d, yyyy - h:mmaaa');
    return formattedDate || '';
  }

  load(appointmentID: number)
  {
    let request = new GetActivitiesAppointmentRequest();
    request.AppointmentID = appointmentID;
    this.appointmentService.GetAppointMent(request).subscribe(response =>
    {
      this.model = response?.ActivitiesAppointments[0];
      this.startDate = this.formatDate(this.model?.StartDate);
      this.defaultPicture = this.model?.EventProfilePhotoURL ?? this.model?.ServiceProfilePhotoURL ?? '/assets/images/tei-logo.png';
    });
  }

  navigateToService()
  {
    this.router.navigate([`activities-services/${ this.LocationID }/${ this.LocationName }`], { relativeTo: this.activatedRoute });
  }
}
