import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';
import { Observable } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { ActivitiesLocation } from '../../@intermediate/activities-locations-settings/models/get-locations.model';
import { IActivitiesLocationsService } from '../../@intermediate/activities-locations/activities-locations.interface';

@Component({
  selector: 'tei-booking-activities-locations',
  templateUrl: './activities-locations.component.html',
  styleUrls: ['./activities-locations.component.scss']
})
export class ActivitiesLocationsComponent implements OnInit
{

  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.LOCATIONS"),
      relativeUrl: ''
    }
  ];

  form: UntypedFormGroup;

  constructor(
    @Inject("IActivitiesLocationsService") private locationsService: IActivitiesLocationsService,
    private translate: TranslateService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  )
  {
  }
  locations$: Observable<ActivitiesLocation[]> = this.locationsService.Get(null).pipe(
    map(res => res.Locations),
    map(res => res.filter(item => item.Active)),
    tap((locations) =>
      locations.length === 1 ? this.router.navigate(['activities-services', locations[0].LocationID, locations[0].LocationName], { relativeTo: this.activatedRoute }) : null)
  );

  ngOnInit()
  {

  }
}
