import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';

const routes: Routes = [
  {
    path: '',
    loadChildren: () => import('./pages/activities-locations/activities-locations.module').then(m => m.ActivitiesLocationsModule),
    data: { RouteData: new RouteData({ Title: 'Locations' }) }
  },
  {
    path: 'confirmation',
    loadChildren: () => import('./pages/confirmation/confirmation.module').then(m => m.ConfirmationModule),
    data: { RouteData: new RouteData({ Title: 'Confirmation' }) }
  },
  {
    path: 'reservation-details/:appointmentID',
    loadChildren: () => import('./pages/reservation-details/reservation-details.module').then(m => m.ReservationDetailsModule),
    data: { RouteData: new RouteData({ Title: 'Reservation Details' }) }
  },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ActivitiesRoutingModule
{
}
