import { UntypedFormArray, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';

export class PostActivitiesServicesRequest
{
  LocationID: number;
  ServiceID: number;
  EmployeeID: number;
  GroupSize: number;
  Notes: string;
  StartTime: string;
  CustomerID: number;
}

export class AvailabilityServicesRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super(
      {
        Date: new UntypedFormControl(new Date(), [Validators.required]),
        Time: new UntypedFormControl([Validators.required]),
        Duration: new UntypedFormControl(60, [Validators.required]),
        PartySize: new UntypedFormControl(1, [Validators.required]),
        Employee: new UntypedFormControl(null, [Validators.required]),
        DynamicFields: new UntypedFormArray([]),
      }
    );
  }
}


export class PostActivitiesServicesRequestForm extends AvailabilityServicesRequestForm
{
  constructor()
  {
    super();
    this.addControl('TermsAndConditions', new UntypedFormControl('', Validators.required));
    this.addControl('ContactsList', new UntypedFormArray([], Validators.required));
  }
}

export class PostActivitiesServicesResponse
{
}
