import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetActivityServicesRequest, GetActivityServicesResponse, IActivityServicesService, PostActivityServicesRequest, PostActivityServicesResponse } from '../@intermediate/activity-services';
import { PostDeactivateServiceRequest, PostDeactivateServiceResponse } from '../@intermediate/activity-services/models/post-deactivate-service.model';

@Injectable({
  providedIn: 'root'
})

export class ActivityServicesService implements IActivityServicesService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl('Booking/Activities') + '/ActivityServices';
  }

  public Get(request: GetActivityServicesRequest): Observable<GetActivityServicesResponse>
  {
    const apiUrl = this.serviceUrl + '/GetActivityServices';
    return this.http.post<GetActivityServicesResponse>(apiUrl, request);
  }

  public Post(request: PostActivityServicesRequest): Observable<PostActivityServicesResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostActivityServicesResponse>(apiUrl, request);
  }

  public PostDeactivate(request: PostDeactivateServiceRequest): Observable<PostDeactivateServiceResponse>
  {
    const apiUrl = this.serviceUrl + '/DeactivateService';
    return this.http.post<PostDeactivateServiceResponse>(apiUrl, request);
  }
}
