import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { IActivitiesAdvancedSettingsService } from '../../@intermediate/activities-advanced-settings/activities-advanced-settings.interface';
import { GetActivitiesAdvancedSettingsRequest, GetActivitiesAdvancedSettingsResponse } from '../../@intermediate/activities-advanced-settings/models/get-activities-advanced-settings.model';
import { PosActivitiesAdvancedSettingsResponse, PostActivitiesAdvancedSettingsRequest } from '../../@intermediate/activities-advanced-settings/models/post-activities-advanced-settings.model';

@Component({
  selector: 'tei-booking-activities-advanced-settings',
  templateUrl: './activities-advanced-settings.component.html',
  styleUrls: ['./activities-advanced-settings.component.scss'],
})
export class ActivitiesAdvancedSettingsComponent implements OnInit
{
  subscription: Subscription = new Subscription();
  breadCrumbData: BreadCrumb[] = [{
    title: this.translate.instant('ACTIVITIES.SETTINGS.ACTIVITIES'),
    relativeUrl: '../'
  },
  {
    title: this.translate.instant('ACTIVITIES.SETTINGS.ADVANCED_SETTINGS'),
    relativeUrl: ''
  }];
  isLoading: boolean;
  form: UntypedFormGroup = new UntypedFormGroup({
    SiteUrl: new UntypedFormControl(''),
    APIUrl: new UntypedFormControl(''),
    ApplicationKey: new UntypedFormControl(''),
  });
  activitiesAdvancedSettingsResponse = new GetActivitiesAdvancedSettingsResponse();
  isSmallScreen: boolean;

  constructor(
    @Inject("IActivitiesAdvancedSettingsService") private service: IActivitiesAdvancedSettingsService,
    private translate: TranslateService,
    public loadingService: ManageLoadingService,
    private router: Router,
    private responsiveService: ResponsiveService,
    private snackBar: SnackBarService)
  { }

  ngOnInit(): void
  {
    this.loadData();
    this.detectMobileScreen();
  }

  private detectMobileScreen(): void
  {
    this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isSmallScreen = screenSize === 'sm' || screenSize === 'xs';
    });
  }

  loadData(): void
  {
    this.isLoading = true;
    const request = new GetActivitiesAdvancedSettingsRequest();

    this.service.Get(request).subscribe(
      (result: GetActivitiesAdvancedSettingsResponse) =>
      {
        this.form.setValue({ SiteUrl: result.SiteUrl, APIUrl: result.APIUrl, ApplicationKey: result.ApplicationKey });
        this.activitiesAdvancedSettingsResponse = result;
        this.isLoading = false;
      },
      (err) =>
      {
        this.isLoading = false;
        this.snackBar.showError(err.message);
      });
  }

  saveSetting(): void
  {
    let request = new PostActivitiesAdvancedSettingsRequest();
    request = this.activitiesAdvancedSettingsResponse;
    request.ApplicationKey = this.form.get('ApplicationKey').value;
    request.SiteUrl = this.form.get('SiteUrl').value;
    request.APIUrl = this.form.get('APIUrl').value;

    this.service.Post(request).subscribe((response: PosActivitiesAdvancedSettingsResponse) =>
    {
      if (response.Success)
      {
        this.snackBar.showError(this.translate.instant('ACTIVITIES.ADVANCED_SETTINGS.SUCCESS_MESSAGE'), "", null, "left", "bottom");
        this.router.navigate(['office/activities/settings']);
      }
    },
      (err) =>
      {
        this.snackBar.showError(err.message);
      });
  }
}
