import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ChipItemModel, ChipOptionModel, ChipType, TreeNestedNode } from '@tei/material';
import { firstValueFrom } from 'rxjs';
import { IActivityCategoryService } from '../../@intermediate/activity-category/activity-category.interface';
import { ActivityCategory, GetActivityCategoryRequest } from '../../@intermediate/activity-category/models/get-activity-category.model';
import { ActivityTypeEnum, GetActivitiesAvailableServicesRequest } from '../../@intermediate/booking-activities/models/get-booking-activities.model';

@Component({
  selector: 'tei-booking-activities-booking-activities-filter',
  templateUrl: './booking-activities-filter.component.html',
  styleUrls: ['./booking-activities-filter.component.scss'],
})
export class BookingActivitiesFilter implements OnInit
{
  @Input() filters = new GetActivitiesAvailableServicesRequest();
  @Output() onFilter = new EventEmitter<Partial<GetActivitiesAvailableServicesRequest>>();
  ChipType = ChipType;
  listItems: TreeNestedNode[] = [];
  types = [
    { Type: this.translate.instant('ACTIVITIES.ACTIVITIES_FILTERS.EVENTS'), Value: ActivityTypeEnum.Event },
    { Type: this.translate.instant('ACTIVITIES.ACTIVITIES_FILTERS.SERVICES'), Value: ActivityTypeEnum.Service }
  ];
  typeChipItemsWithDisplayName = [
    {
      chipName: 'Type',
      displayName: this.translate.instant('ACTIVITIES.ACTIVITIES_FILTERS.TYPE'),
      bindValue: 'Value'
    }
  ];
  categorySelectedChip: ChipOptionModel = {
    firstValue: null,
    secondValue: null,
    displayName: null,
    hasValue: false
  };
  constructor(
    @Inject('IActivityCategoryService') private activityCategoryService: IActivityCategoryService,
    private translate: TranslateService
  )
  {
  }
  async ngOnInit(): Promise<void>
  {
    const categoriesRequest: GetActivityCategoryRequest = {};

    const categoriesResponse = await firstValueFrom(this.activityCategoryService.Get(categoriesRequest));

    this.listItems = categoriesResponse.ActivityCategories.map(obj => this.mapTree(obj));

    if (this.filters?.CategoryIDs)
    {
      this.categorySelectedChip.firstValue = this.filters.CategoryIDs;
      this.categorySelectedChip.hasValue = true;
    }

    if (this.filters?.ActivityType)
    {
      this.typeChipItemsWithDisplayName[0]["selectedItem"] = this.filters.ActivityType;
      this.typeChipItemsWithDisplayName[0]["chipsState"] = "selected";
      this.typeChipItemsWithDisplayName = JSON.parse(JSON.stringify(this.typeChipItemsWithDisplayName));
    }
  }


  mapTree(category: ActivityCategory): TreeNestedNode
  {
    const targetNode: TreeNestedNode = {
      title: category.CategoryName,
      value: category.ActivityCategoryID,
    };
    if (category.Childs?.length > 0)
    {
      targetNode.children = category.Childs.map(child => this.mapTree(child));
    }

    return targetNode;
  }


  onChangeCategory(chipOptionModel: ChipOptionModel): void
  {
    if (chipOptionModel.firstValue)
    {
      this.filters.CategoryIDs = chipOptionModel.firstValue;
    }
    else
    {
      delete this.filters.CategoryIDs;
    }
    this.onFilter.emit(this.filters);
  }

  enterSearchValue(searchInput: HTMLInputElement): void
  {
    if (searchInput.value)
    {
      this.filters.SearchString = searchInput.value;
    } else
    {
      delete this.filters.SearchString;
    }
    this.onFilter.emit(this.filters);
  }

  onChangeType(data: ChipItemModel[]): void
  {
    if (data?.[0]?.value != undefined)
    {
      this.filters.ActivityType = data?.[0]?.value as unknown as ActivityTypeEnum;
    }
    else
    {
      delete this.filters.ActivityType;
    }

    this.onFilter.emit(this.filters);
  }
}
