import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';;
import { IActivitiesLocationsService } from '../@intermediate/activities-locations/activities-locations.interface';
import { GetActivitiesLocationsRequest, GetActivitiesLocationsResponse } from '../@intermediate/activities-locations/models/get-locations.model';

@Injectable({
  providedIn: 'root'
})

export class ActivitiesLocationsService implements IActivitiesLocationsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/Booking/Activities/Locations";
  }

  Get(request: GetActivitiesLocationsRequest): Observable<GetActivitiesLocationsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetLocations';
    return this.http.get<GetActivitiesLocationsResponse>(apiUrl, { params: request as any });
  }

}
