import { UntypedFormArray, UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { ActivityService, GetActivityServicesResponse } from './get-activity-services.model';

export class PostActivityServicesRequest
{
  //public Title: string;
}

export class PostActivityServicesRequestForm extends UntypedFormGroup
{
  public readonly activityServices: UntypedFormArray;

  constructor()
  {
    super({
      ActivityServices: new UntypedFormArray([])
    });

    this.activityServices = this.get('ActivityServices') as UntypedFormArray;
  }

  //public value: PostActivityServicesRequest;

  public setValue(value: GetActivityServicesResponse)
  {
    const request = new PostActivityServicesRequest();

    value.ActivityServices?.forEach(ass =>
    {
      let asf = new ActivityServiceFormGroup();
      asf.setValue(ass);
      this.activityServices.push(asf);
    });

    super.setValue(value);
  }
}

export class ActivityServiceFormGroup extends UntypedFormGroup
{

  public readonly activityLocations: UntypedFormArray;

  constructor()
  {
    super({
      ActivityServiceID: new UntypedFormControl(),
      PortalID: new UntypedFormControl(),
      ExternalServiceID: new UntypedFormControl(),
      ServiceName: new UntypedFormControl(),
      ServiceDesc: new UntypedFormControl(),
      CategoryName: new UntypedFormControl(),
      ProfilePhotoURL: new UntypedFormControl(),
      IsActive: new UntypedFormControl(),
      Details: new UntypedFormControl(),
      ActivityLocations: new UntypedFormArray([])
    });

    this.activityLocations = this.get('ActivityLocations') as UntypedFormArray;

  }

  public setValue(activityService: ActivityService, options?: { onlySelf?: boolean; emitEvent?: boolean; }): void
  {
    this.activityLocations.clear();

    activityService.ActivityLocations.forEach(al =>
    {
      let fg = new ActivityLocationFormGroup();
      fg.setValue(al);

      this.activityLocations.push(fg);
    });

    super.setValue(activityService);

  }
}

export class ActivityLocationFormGroup extends UntypedFormGroup
{


  constructor()
  {
    super({
      ActivityServiceLocationID: new UntypedFormControl(),
      ActivitiesLocationID: new UntypedFormControl(),
      LocationName: new UntypedFormControl(''),
    });
  }

}

