import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { ActivitiesSettingsComponent } from './pages/activities-settings/activities-settings.component';

const routes: Routes = [

  {
    path: '',
    component: ActivitiesSettingsComponent,
    children: []
  },
  {
    path: 'locations',
    loadChildren: () => import('./pages/activities-locations-settings/activities-locations-settings.module').then(m => m.ActivitiesLocationsSettingsModule),
    data: { RouteData: new RouteData({ Title: 'locations' }) }
  },
  {
    path: 'events',
    loadChildren: () => import('./pages/activities-events-settings/activities-events-settings.module').then(m => m.ActivitiesEventsSettingsModule)
  },
  {
    path: 'categories',
    loadChildren: () => import('./pages/activities-categories-settings/activities-categories-settings.module').then(m => m.ActivitiesCategoriesSettingsModule)
  },
  {
    path: 'advanced-settings',
    loadChildren: () => import('./pages/activities-advanced-settings/activities-advanced-settings.module').then(m => m.ActivitiesAdvancedSettingsModule)
  },
  {
    path: 'manage-event',
    loadChildren: () => import('./pages/manage-event/manage-event.module').then(m => m.ManageEventModule)
  },
  {
    path: 'manage-service',
    loadChildren: () => import('./pages/manage-service/manage-service.module').then(m => m.ManageServiceModule)
  },
  {
    path: 'manage-service/:serviceID',
    loadChildren: () => import('./pages/manage-service/manage-service.module').then(m => m.ManageServiceModule)
  },
  {
    path: 'services',
    loadChildren: () => import('./pages/activity-services/activity-services.module').then(m => m.ActivityServicesModule)
  },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ActivitiesSettingRoutingModule
{
}
