import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatIconModule } from '@angular/material/icon';
import { RouterModule } from '@angular/router';
import { RouteData } from '@tei/core';
import { TeiTextEllipsisPipeModule } from '@tei/core-translate';
import { BreadCrumbModule, NumericPadModule } from '@tei/material';
import { ActivitiesLocationsService } from '../../@services/activities-locations.service';
import { ActivitiesLocationsComponent } from './activities-locations.component';

const MATERIAL_MODULES = [MatIconModule, MatButtonModule, MatCardModule];
const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, NumericPadModule];
const TEI_MODULES = [TeiTextEllipsisPipeModule];

@NgModule({
  declarations: [ActivitiesLocationsComponent],
  imports: [
    ...TEI_MODULES,
    ...MATERIAL_MODULES,
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES,
    RouterModule.forChild([
      { path: '', component: ActivitiesLocationsComponent },
      {
        path: 'confirmation/:ConfirmationNumber/:LocationID/:LocationName',
        loadChildren: () => import('../confirmation/confirmation.module').then(m => m.ConfirmationModule),
        data: { RouteData: new RouteData({ Title: 'Tennis Center' }) }
      },
      {
        path: 'activities-services/:id/:name',
        loadChildren: () => import('../booking-activities-page/booking-activities-page.module').then(m => m.BookingActivitiesPageModule),
        data: { RouteData: new RouteData({ Title: 'Tennis Center' }) }
      },
    ]),
  ],
  providers: [{ provide: 'IActivitiesLocationsService', useClass: ActivitiesLocationsService }]
})
export class ActivitiesLocationsModule
{ }
