import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, ChipItemModel, ConfirmationDialogComponent, SnackBarService, StateOfChips } from '@tei/material';
import { IActivitiesLocationsSettingsService } from '../../@intermediate/activities-locations-settings/activities-locations-settings.interface';
import { DeleteLocationRequest } from '../../@intermediate/activities-locations-settings/models/delete-location.model';
import { ActivitiesLocation, GetActivitiesLocationsSettingsRequest } from '../../@intermediate/activities-locations-settings/models/get-locations.model';

@Component({
  selector: 'tei-booking-activities-locations-settings',
  templateUrl: './activities-locations-settings.component.html',
  styleUrls: ['./activities-locations-settings.component.scss']
})
export class ActivitiesLocationsSettingsComponent implements OnInit
{
  locationStatuses = [
    { Status: 'Active', Value: true },
    { Status: 'Inactive', Value: false },];
  statusChipItemsWithDisplayName = [{ chipName: 'Status', displayName: 'Status', bindValue: 'Value', chipsState: StateOfChips.Selected, selectedItem: 'Active' },
  ];
  dataSource = new MatTableDataSource<ActivitiesLocation>();
  isLoading = false;
  displayedColumns: string[] = ['LocationID', 'LocationName', 'Status', 'Actions', 'MobileMode'];
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.ACTIVITIES"),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.LOCATIONS"),
      relativeUrl: ''
    },
  ];
  form: UntypedFormGroup;

  constructor(public dialog: MatDialog,
    @Inject("IActivitiesLocationsSettingsService") private locationsSettingsService: IActivitiesLocationsSettingsService,
    private snackBar: SnackBarService,
    private activeRoute: ActivatedRoute,
    private translate: TranslateService,
    private router: Router
    )
  {
  }
  navigateToManageLocation(locationID?: string): void
  {
    this.router.navigate(['manage-location', locationID], { relativeTo: this.activeRoute });
  }

  ngOnInit()
  {
    const request: GetActivitiesLocationsSettingsRequest = {
      Active: true
    };
    this.loadData(request);
  }
  deleteLocation(item: any)
  {
    const dialogData = {
      dialogData: {
        title: 'Delete Location',
        content: `Are you sure you want to delete this location?`,
        submitButtonLabel: 'Delete',
        submitButtonColor: 'warn'
      }
    };

    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      maxWidth: '18vw',
      minWidth: '320px',
      panelClass: 'o-dialog-change-preferences',
      data: dialogData
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.delete(item.LocationID);
      }

    });
  }

  delete(locationID: number)
  {
    let request: DeleteLocationRequest = {
      LocationID: locationID
    };
    this.isLoading = true;
    this.locationsSettingsService.Delete(request).subscribe(
      () =>
      {
        this.isLoading = false;
        this.snackBar.showSuccess("Location deleted successfully.");
        this.loadData(new GetActivitiesLocationsSettingsRequest());
      },
      (err) =>
      {
        this.isLoading = false;
      });
  }


  onStatusFilterChange(filterObjects: Array<ChipItemModel>): void
  {
    let chipItem = filterObjects[0];
    let request = new GetActivitiesLocationsSettingsRequest();
    if (chipItem.value != undefined)
    {
      request.Active = chipItem.value !== 'Inactive';
    }
    else
    {
      delete request.Active;
    }
    this.loadData(request);
  }

  loadData(request: GetActivitiesLocationsSettingsRequest): void
  {
    this.isLoading = true;
    this.locationsSettingsService.Get(request).subscribe(
      (result) =>
      {
        this.isLoading = false;
        this.dataSource = new MatTableDataSource<ActivitiesLocation>(result.Locations);
      },
      (err) =>
      {
        this.isLoading = false;
        this.snackBar.showError(err.message);
      });

  }
}
