import { AfterViewInit, Component, forwardRef, Input, OnInit } from '@angular/core';
import { ControlValueAccessor, FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
@Component({
  selector: 'tei-manage-event-quill-container',
  templateUrl: './quill-container.component.html',
  styleUrls: ['./quill-container.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => QuillContainerComponent),
      multi: true
    }
  ]
})
export class QuillContainerComponent implements ControlValueAccessor, AfterViewInit
{
  editor = new FormControl();

  private onChange: (value: string) => void;
  private onTouched: () => void;
  isDisabled: boolean = false;

  constructor() { }

  writeValue(value: string): void {
    this.editor.setValue(value);
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  onBlur(): void {
    this.onTouched();
  }

  ngAfterViewInit(): void {
    this.editor.valueChanges.subscribe(this.onChange);
  }

}
