import { Component, Input, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';

@Component({
  selector: 'tei-booking-activities-tei-chips',
  templateUrl: './tei-chips.component.html',
  styleUrls: ['./tei-chips.component.scss'],
})
export class TeiChipsComponent implements OnInit
{

  @Input('teiChipData')
  teiChipData: TeiChipsModel;
  @Input('chipsFormGroup')
  formGroup = new UntypedFormGroup({});


  @Input('chipFormControlName')
  chipFormControlName = '';

  constructor()
  {
  }

  ngOnInit(): void
  {

  }


  onToppingRemoved(topping: number)
  {
    const toppings = this.internalFormControl.value as number[];
    this.removeFirst(toppings, topping);
    this.internalFormControl.setValue(toppings); // To trigger change detection
  }

  private removeFirst<T>(array: T[], toRemove: T): void
  {
    const index = array.indexOf(toRemove);
    if (index !== -1)
    {
      array.splice(index, 1);
    }
  }

  public get internalFormControl(): UntypedFormControl
  {
    return (this.formGroup.get(this.chipFormControlName) as UntypedFormControl);
  }

  public valueById(id: number): string
  {
    return this.teiChipData.allChips.find(c => c.id === id)?.name || '';
  }
}



export class TeiChipsModel
{
  allChips: { id, name, active, code?; }[];
}
