import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IActivitiesLocationsSettingsService } from '../@intermediate/activities-locations-settings/activities-locations-settings.interface';
import { DeleteLocationRequest, DeleteLocationResponse } from '../@intermediate/activities-locations-settings/models/delete-location.model';
import { GetActivitiesLocationsSettingsRequest, GetActivitiesLocationsSettingsResponse } from '../@intermediate/activities-locations-settings/models/get-locations.model';
import {  PostLocationResponse } from '../@intermediate/activities-locations-settings/models/post-location.model';

@Injectable({
  providedIn: 'root'
})

export class ActivitiesLocationsSettingsService implements IActivitiesLocationsSettingsService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/Booking/Activities/Locations";
  }
  Post(request: FormData): Observable<PostLocationResponse>
  {
    const apiUrl = this.serviceUrl + '/PostLocation';
    return this.http.post<PostLocationResponse>(apiUrl, request);
  }
  Delete(request: DeleteLocationRequest): Observable<DeleteLocationResponse>
  {
    const apiUrl = this.serviceUrl + '/DeleteLocation';
    return this.http.delete<DeleteLocationResponse>(apiUrl, { params: request as any });
  }
  Get(request: GetActivitiesLocationsSettingsRequest): Observable<GetActivitiesLocationsSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetLocations';
    return this.http.get<GetActivitiesLocationsSettingsResponse>(apiUrl, { params: request as any });
  }

}
