import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatDividerModule } from '@angular/material/divider';
import { RouterModule } from '@angular/router';
import { ContactLookupSharedModule, ContactSelectionWrapperModule } from '@tei/common-contact-lookup-shared';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule } from '@tei/material';
import { AppointmentsService } from '../../@services/appointments.service';
import { ReservationDetailsComponent } from './reservation-details.component';


const BASE_MODULES = [
  CommonModule,
  FormsModule,
  ReactiveFormsModule
];
const TEI_BASE_MODULES = [
  TeiTextTranslatePipeModule,
  TeiDateTranslateModule,
  TeiCurrencyTranslateModule,
  ContactLookupSharedModule,
  ContactSelectionWrapperModule
];
const MATERIAL_MODULES = [
  MatButtonModule,
  MatCardModule,
  MatDividerModule
];
const TEI_MATERIAL_MODULES = [BreadCrumbModule];


@NgModule({
  declarations: [ReservationDetailsComponent],
  imports: [
    ...MATERIAL_MODULES,
    RouterModule.forChild([
      { path: '', component: ReservationDetailsComponent },
    ]),
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_BASE_MODULES,
  ],
  providers: [{ provide: 'IAppointmentsService', useClass: AppointmentsService }],
})
export class ReservationDetailsModule
{ }
