import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ChipItemModel, ChipOptionModel, ChipType, StateOfChips, TreeNestedNode } from '@tei/material';
import { firstValueFrom } from 'rxjs';
import { IActivitiesLocationsSettingsService } from '../../@intermediate/activities-locations-settings/activities-locations-settings.interface';
import { ActivitiesLocation, GetActivitiesLocationsSettingsRequest } from '../../@intermediate/activities-locations-settings/models/get-locations.model';
import { IActivityCategoryService } from '../../@intermediate/activity-category/activity-category.interface';
import { ActivityCategory, GetActivityCategoryRequest, GetActivityCategoryResponse } from '../../@intermediate/activity-category/models/get-activity-category.model';
import { GetActivityServicesRequest } from '../../@intermediate/activity-services';


@Component({
  selector: 'tei-manage-activity-services-filter',
  templateUrl: './activity-services-filter.component.html',
  styleUrls: ['./activity-services-filter.component.scss'],

})
export class ActivityServicesFilterComponent implements OnInit
{
  ChipType = ChipType;
  statuses = [
    { Status: this.translate.instant('ACTIVITIES.ACTIVITY_SERVICES.ACTIVE'), Value: 'true' },
    { Status: this.translate.instant('ACTIVITIES.ACTIVITY_SERVICES.INACTIVE'), Value: 'false' }
  ];
  statusChipItemsWithDisplayName = [
    {
      chipName: 'Status',
      displayName: this.translate.instant('ACTIVITIES.ACTIVITY_SERVICES.STATUS'),
      bindValue: 'Value',
      chipsState: StateOfChips.Selected,
      selectedItem: this.translate.instant('ACTIVITIES.ACTIVITY_SERVICES.ACTIVE')
    }
  ];
  locations: ActivitiesLocation[];
  locationChipItemsWithDisplayName = [
    {
      chipName: 'LocationName',
      displayName: this.translate.instant('ACTIVITIES.ACTIVITY_SERVICES.LOCATION'),
      bindValue: 'LocationID'
    }
  ];
  listItems: TreeNestedNode[] = [];
  filters: GetActivityServicesRequest = {
    IsActive: true
  };
  categories: GetActivityCategoryResponse;

  @Output() onFilter = new EventEmitter<Partial<GetActivityServicesRequest>>();

  constructor(
    private translate: TranslateService,
    @Inject('IActivitiesLocationsSettingsService') private activitiesLocationsSettingsService: IActivitiesLocationsSettingsService,
    @Inject('IActivityCategoryService') private activityCategoryService: IActivityCategoryService
  )
  {

  }
  async ngOnInit(): Promise<void>
  {
    if (Object.keys(this.filters).length > 0)
    {
      this.onFilter.emit(this.filters);
    }

    const request: GetActivitiesLocationsSettingsRequest = {
    };

    const response = await firstValueFrom(this.activitiesLocationsSettingsService.Get(request));

    this.locations = response.Locations;

    const categoriesRequest: GetActivityCategoryRequest = {
    };

    const categoriesResponse = await firstValueFrom(this.activityCategoryService.Get(categoriesRequest));

    this.categories = categoriesResponse;

    this.listItems = categoriesResponse.ActivityCategories.map(obj => this.mapTree(obj));
  }

  mapTree(category: ActivityCategory): TreeNestedNode
  {
    const targetNode: TreeNestedNode = {
      title: category.CategoryName,
      value: category.ActivityCategoryID,
    };
    if (category.Childs?.length > 0)
    {
      targetNode.children = category.Childs.map(child => this.mapTree(child));
    }

    return targetNode;
  }

  onChangeServiceID(data: ChipOptionModel): void
  {
    if (data.firstValue)
    {
      this.filters.ExternalServiceID = data.firstValue;
    } else
    {
      delete this.filters.ExternalServiceID;
    }
    this.onFilter.emit(this.filters);
  }

  onChangeServiceName(data: ChipOptionModel): void
  {
    if (data.firstValue)
    {
      this.filters.ServiceName = data.firstValue;
    } else
    {
      delete this.filters.ServiceName;
    }
    this.onFilter.emit(this.filters);
  }

  onChangeLocations(data: ChipItemModel[]): void
  {
    if (data?.[0]?.value)
    {
      this.filters.LocationIDs = (data?.[0]?.value as string[]).map(v => +v);
      this.onFilter.emit(this.filters);
    }
  }

  clearAllLocations(): void
  {
    delete this.filters.LocationIDs;
    this.onFilter.emit(this.filters);
  }

  onChangeCategory(chipOptionModel: ChipOptionModel): void
  {
    if (chipOptionModel.firstValue)
    {
      this.filters.CategoryIDs = chipOptionModel.firstValue;
    }
    else
    {
      delete this.filters.CategoryIDs;
    }
    this.onFilter.emit(this.filters);
  }

  onChangeStatus(data: ChipItemModel[]): void
  {
    if (data?.[0]?.value != undefined)
    {
      this.filters.IsActive = data?.[0]?.value === 'true';
    }
    else
    {
      delete this.filters.IsActive;
    }

    this.onFilter.emit(this.filters);
  }
}
