import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetManageEventRequest, GetManageEventResponse, IManageEventService, PostManageEventRequest, PostManageEventResponse } from '../@intermediate/manage-event';

@Injectable({
  providedIn: 'root'
})

export class ManageEventService implements IManageEventService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl('Booking/Activities') + '/ActivityEvents';
  }

  public Get(request: GetManageEventRequest): Observable<GetManageEventResponse>
  {
    const apiUrl = this.serviceUrl + '/GetEvent';
    return this.http.get<GetManageEventResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostManageEventRequest): Observable<PostManageEventResponse>
  {
    const apiUrl = this.serviceUrl + '/PostEvent';

    return this.http.post<PostManageEventResponse>(apiUrl, request);
  }

}
