import { formatDate } from '@angular/common';
import { Component, Inject, Injectable, OnInit } from '@angular/core';
import { UntypedFormControl } from '@angular/forms';
import { DateAdapter, MAT_DATE_FORMATS, NativeDateAdapter } from '@angular/material/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb, ImageUploaderComponent, ImageUploaderModel, SnackBarService } from '@tei/material';
import { DesignationBasedSettingsModel } from '@tei/material-smart-components';
import moment from 'moment';
import { Observable, Subject, catchError, concat, debounceTime, firstValueFrom, of, switchMap } from 'rxjs';
import { GetManageServiceRequest, GetManageServiceResponse, IManageServiceService, PostManageServiceRequestForm } from '../../@intermediate/manage-service';
import { GetPriceOverrideItemsRequest, RetailItem } from '../../@intermediate/retail-items/models/get-price-override-items.model';
import { IRetailItemsService } from '../../@intermediate/retail-items/retail-items.interface';
import { TeiChipsModel } from '../../components/tei-chips/tei-chips.component';

export const PICK_FORMATS = {
  parse: { dateInput: { month: 'short', year: 'numeric', day: 'numeric' } },
  display: {
    dateInput: 'input',
    monthYearLabel: { year: 'numeric', month: 'short' },
    dateA11yLabel: { year: 'numeric', month: 'long', day: 'numeric' },
    monthYearA11yLabel: { year: 'numeric', month: 'long' }
  }
};


@Injectable()
export class PickDateAdapter extends NativeDateAdapter
{
  format(date: Date, displayFormat: Object): string
  {
    if (displayFormat === 'input')
    {
      return formatDate(date, 'EEE MMM d, y', this.locale);
    }
    else
    {
      return date.toDateString();
    }
  }
}


@Component({
  selector: 'tei-activities-pages-manage-service',
  templateUrl: './manage-service.component.html',
  styleUrls: ['./manage-service.component.scss'],
  providers: [
    { provide: DateAdapter, useClass: PickDateAdapter },
    { provide: MAT_DATE_FORMATS, useValue: PICK_FORMATS }
  ]
})
export class ManageServiceComponent implements OnInit
{

  model: GetManageServiceResponse;
  settings: DesignationBasedSettingsModel = {};
  form: PostManageServiceRequestForm = new PostManageServiceRequestForm();
  startTime = new Date(1995, 10, 17, 0, 0, 0);
  endTime = new Date(1995, 10, 17, 23, 45, 0);
  serviceID: number;
  tempImage: any = '/assets/images/tei-logo.png';
  isLoading = false;

  teiChipData: TeiChipsModel = {
    allChips: []
  };
  teiCategoriesChipData: TeiChipsModel = {
    allChips: []
  };
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.ACTIVITIES"),
      relativeUrl: '/office/activities/settings',
    },
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.SERVICES"),
      relativeUrl: '/office/activities/settings/services'
    },
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.MANAGE_SERVICE"),
      relativeUrl: ''
    },
  ];
  priceOverrideItemArray$: Observable<RetailItem[]>;
  priceOverrideItemTypeaheadInput$ = new Subject<string>();
  retailItems: RetailItem[];
  editMode: boolean;
  isSmallScreen = false;

  constructor(
    @Inject('IManageServiceService') private ManageServiceService: IManageServiceService,
    @Inject("IRetailItemsService") private retailItemsService: IRetailItemsService,
    private responsiveService: ResponsiveService,
    public dialog: MatDialog,
    private activatedRoute: ActivatedRoute,
    private snackBar: SnackBarService,
    private translate: TranslateService,
    private router: Router
  )
  {

  }

  ngOnInit(): void
  {
    this.serviceID = +this.activatedRoute.snapshot.paramMap.get('serviceID');
    const request = new GetManageServiceRequest();
    if (this.serviceID)
    {
      this.editMode = true;
      request.ActivitiesServiceID = this.serviceID;
    }
    request.ActivitiesServiceID = this.serviceID;
    this.load(request);
    this.searchPriceOverrideItems();
    this.checkScreenSize();
  }

  getFormControlByName(name: string): UntypedFormControl
  {
    return this.form.get(name) as UntypedFormControl;
  }

  private async load(request: GetManageServiceRequest): Promise<void>
  {
    try
    {
      this.isLoading = true;

      const priceOverrideItemsRequest: GetPriceOverrideItemsRequest = {};
      const priceOverrideItemsResponse = await firstValueFrom(this.retailItemsService.PriceOverrideItems(priceOverrideItemsRequest));
      this.retailItems = priceOverrideItemsResponse.RetailItems;
      if (!this.model)
      {
        this.model = new GetManageServiceResponse();
      }
      const response = await firstValueFrom(this.ManageServiceService.Get(request));
      this.model = response;
      this.settings = this.model.PermissionTable;
      // Frontend data
      if (!this.model.ProfilePhotoURL)
      {
        this.model.ProfilePhotoURL = '/assets/images/tei-logo.png';
      }

      this.teiChipData = {
        allChips: [],
      };
      this.teiChipData.allChips = this.model?.AllActivityLocations.sort((a, b) => a.LocationName.localeCompare(b.LocationName)).map(l => ({ id: l.ActivitiesLocationID, name: l.LocationName, active: true }));
      this.teiCategoriesChipData.allChips = this.model?.AllActivityCategories.sort((a, b) => a.CategoryName.localeCompare(b.CategoryName)).map(l => ({ id: l.ActivityCategoryID, name: l.CategoryName, active: l.Active || true, code: l.CategoryCode }));
      this.form.setValue(response);
      this.onPriceOverrideItemsChange(response.BackOfficeItemID);
      this.isLoading = false;

    }
    catch (error)
    {
      this.isLoading = false;
    }
  }

  private checkScreenSize(): void
  {
    const screenSize = this.responsiveService.breakPoint;
    this.isSmallScreen = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
  }

  onPriceOverrideItemsChange(itemID: string): void
  {
    this.form.controls.BackOfficeItemID.setValue(itemID);
  };

  private searchPriceOverrideItems(): void
  {
    this.priceOverrideItemArray$ = concat(
      of([]), // default items
      this.priceOverrideItemTypeaheadInput$.pipe(
        debounceTime(300),
        switchMap((searchTerm = '') =>
          this.getPriceOverrideItems(searchTerm).pipe(
            catchError(() => of([])),
          ))
      )
    );
  }

  getPriceOverrideItems(term: string): Observable<RetailItem[]>
  {
    let items: RetailItem[] = [];

    if (term)
    {
      const lowerTerm = term.toLowerCase();
      items = this.retailItems.filter(item =>
        item.Description?.toLowerCase().includes(lowerTerm) ||
        item.ItemID?.toLowerCase().includes(lowerTerm)
      );
    }
    return of(items);
  }

  openDialog(): void
  {
    const imageFormats = '.jpg,.jpeg,.jpe,.jif,.jfif,.jfi,.png,.gif,.bmp,.dib,.tiff,.tif,.heif,.heic';
    const permittedFileTypes = ['JPG', 'JPEG', 'JPE', 'JFIF', 'PNG', 'GIF', 'BMP', 'TIFF', 'HEIC'];
    const data: ImageUploaderModel = {
      round: false,
      maintainAspectRatio: false,
      aspectRatio: null,
      acceptFormat: imageFormats,
      permittedFileTypes: permittedFileTypes,
      maxSize: 500,
      hideRemoveButton: true,
      imageServerUrl: this.model?.ProfilePhotoURL,
    };

    const imageUploaderDialog = this.dialog.open(ImageUploaderComponent, {
      width: '400px',
      panelClass: ['o-dialog-md-fullscreen'],
      data,
    });

    imageUploaderDialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.model.ProfilePhotoURL = result.croppedImage;
        this.form.controls.Image.setValue(result.croppedImage);
      }
    });
  }

  save(form: PostManageServiceRequestForm): void
  {
    this.isLoading = true;
    let formValue = form.getRawValue();
    formValue.PermissionTable = this.convertNegativeOneToNull(formValue.Settings);
    delete formValue.Settings;
    formValue.EarliestAvailableTime = moment(new Date()).format("YYYY-MM-DD[T]") + moment(formValue?.EarliestAvailableTime).format('HH:mm:ss');
    formValue.LatestAvailableTime = moment(new Date()).format("YYYY-MM-DD[T]") + moment(formValue?.LatestAvailableTime).format('HH:mm:ss');

    this.ManageServiceService.Post(formValue).subscribe({
      next: (response) =>
      {
        this.isLoading = false;
        if (!this.editMode)
        {
          this.router.navigate([response.InsertedServiceID], { relativeTo: this.activatedRoute });
        }
        this.snackBar.showSuccess(this.translate.instant('ACTIVITIES.MANAGE_SERVICE.SAVED_SUCCESSFULLY'), '', null, 'left');

      },
      error: (e) => { this.isLoading = false; },
    });
  }

  convertNegativeOneToNull(obj: Record<string, any>): Record<string, any>
  {
    // Check if obj is an object and not null
    if (typeof obj === "object" && obj !== null)
    {
      // Iterate over the keys in the object
      for (const key in obj)
      {
        if (obj.hasOwnProperty(key))
        {
          // If the property value is exactly "-1", set it to null
          if (obj[key] === "-1")
          {
            obj[key] = null;
          }
          // If the property is an object or an array, recurse into it
          else if (typeof obj[key] === "object")
          {
            this.convertNegativeOneToNull(obj[key]);
          }
        }
      }
    }
    return obj;
  }

}
