import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetPriceOverrideItemsRequest, GetPriceOverrideItemsResponse } from '../@intermediate/retail-items/models/get-price-override-items.model';
import { IRetailItemsService } from '../@intermediate/retail-items/retail-items.interface';

@Injectable({
  providedIn: 'root'
})

export class RetailItemsService implements IRetailItemsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl('Booking/Activities/RetailItems');
  }

  public PriceOverrideItems(request: GetPriceOverrideItemsRequest): Observable<GetPriceOverrideItemsResponse>
  {
    const apiUrl = this.serviceUrl + '/PriceOverrideItems';
    return this.http.get<GetPriceOverrideItemsResponse>(apiUrl, { params: request as any });
  }
}
