import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetPricingRequest, GetPricingResponse } from '../@intermediate/activity-services/models/get-pricing.model';
import { PostAppointmentRequest, PostAppointmentResponse } from '../@intermediate/appointments/models/post-apponiment-services.model';
import { IActivitiesServicesService } from '../@intermediate/booking-activities/booking-activities.interface';
import { GetActivitiesAppointmentRequest, GetActivitiesAppointmentResponse, GetActivitiesAvailabilityRequest, GetActivitiesAvailabilityResponse, GetActivitiesAvailableEmployeesAndResourcesRequest, GetActivitiesAvailableEmployeesAndResourcesResponse, GetActivitiesAvailableServicesRequest, GetActivitiesAvailableServicesResponse, GetContactLookupTemplateRequest, GetContactLookupTemplateResponse, GetEventDatesRequest, GetEventDatesResponse, GetEventOccurrencesRequest, GetEventOccurrencesResponse } from '../@intermediate/booking-activities/models/get-booking-activities.model';
import { PostActivitiesServicesRequest, PostActivitiesServicesResponse } from '../@intermediate/booking-activities/models/post-booking-activities.model';

@Injectable({
  providedIn: 'root'
})

export class BookingActivitiesService implements IActivitiesServicesService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/Booking/Activities/";
  }
  BookServiceAppointment(request: PostAppointmentRequest): Observable<PostAppointmentResponse>
  {
    const apiUrl = this.serviceUrl + 'Appointments/BookServiceAppointment';
    return this.http.post<PostAppointmentResponse>(apiUrl, request);
  }
  BookEventAppointment(request: PostAppointmentRequest): Observable<PostAppointmentResponse>
  {
    const apiUrl = this.serviceUrl + 'Appointments/BookEventAppointment';
    return this.http.post<PostAppointmentResponse>(apiUrl, request);
  }
  GetContactLookupConfig(request: GetContactLookupTemplateRequest): Observable<GetContactLookupTemplateResponse>
  {
    const apiUrl = this.serviceUrl + 'ActivityServices/GetActivitiesPartyDetails';
    return this.http.get<GetContactLookupTemplateResponse>(apiUrl, { params: request as any });

  }
  GetEventOccurrences(request: GetEventOccurrencesRequest): Observable<GetEventOccurrencesResponse>
  {
    const apiUrl = this.serviceUrl + 'ActivityEvents/GetEventOccurrances';
    return this.http.get<GetEventOccurrencesResponse>(apiUrl, { params: request as any });
  }


  GetEventDates(request: GetEventDatesRequest): Observable<GetEventDatesResponse>
  {
    const apiUrl = this.serviceUrl + 'ActivityEvents/GetEventDates';
    return this.http.get<GetEventDatesResponse>(apiUrl, { params: request as any });
  }

  GetActivitiesAvailableEmployeesAndResources(request: GetActivitiesAvailableEmployeesAndResourcesRequest): Observable<GetActivitiesAvailableEmployeesAndResourcesResponse>
  {
    const apiUrl = this.serviceUrl + 'ActivityServices/GetActivitiesAvailableEmployeesAndResources';
    return this.http.get<GetActivitiesAvailableEmployeesAndResourcesResponse>(apiUrl, { params: request as any });
  }

  GetActivitiesAvailableServices(request: GetActivitiesAvailableServicesRequest): Observable<GetActivitiesAvailableServicesResponse>
  {
    const apiUrl = this.serviceUrl + 'ActivityServices/GetActivitiesAvailableServices';
    return this.http.get<GetActivitiesAvailableServicesResponse>(apiUrl, { params: request as any });
  }

  GetActivitiesAvailability(request: GetActivitiesAvailabilityRequest): Observable<GetActivitiesAvailabilityResponse>
  {
    const apiUrl = this.serviceUrl + 'ActivityServices/GetActivitiesAvailability';
    return this.http.post<GetActivitiesAvailabilityResponse>(apiUrl, request);
  }

  Post(request: PostActivitiesServicesRequest): Observable<PostActivitiesServicesResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostActivitiesServicesResponse>(apiUrl, request);
  }

  GetAppointMent(request: GetActivitiesAppointmentRequest): Observable<GetActivitiesAppointmentResponse>
  {
    const apiUrl = this.serviceUrl + 'Appointments/GetActivityAppointments';
    return this.http.get<GetActivitiesAppointmentResponse>(apiUrl, { params: request as any });
  }
  GetPricing(request: GetPricingRequest): Observable<GetPricingResponse>
  {
    const apiUrl = this.serviceUrl + 'Pricing/PostGetPricing';
    return this.http.post<GetPricingResponse>(apiUrl, request);
  }
}
