
import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { MatLegacyCardModule as MatCardModule } from "@angular/material/legacy-card";
import { MatLegacyCheckboxModule as MatCheckboxModule } from "@angular/material/legacy-checkbox";
import { MatLegacyInputModule as MatInputModule } from "@angular/material/legacy-input";
import { MatLegacyMenuModule as MatMenuModule } from "@angular/material/legacy-menu";
import { MatLegacyPaginatorModule as MatPaginatorModule } from "@angular/material/legacy-paginator";
import { MatSortModule } from "@angular/material/sort";
import { MatLegacyTableModule as MatTableModule } from "@angular/material/legacy-table";
import { MatLegacyTabsModule as MatTabsModule } from "@angular/material/legacy-tabs";
import { MatLegacyTooltipModule as MatTooltipModule } from "@angular/material/legacy-tooltip";
import { RouterModule } from "@angular/router";
import { TranslateModule } from "@ngx-translate/core";
import { TeiDateTranslateModule, TeiTextTranslatePipeModule } from "@tei/core-translate";
import { BreadCrumbModule, LoadingModule, QuickSearchFilterChipModule, UploadProfilePictureModule } from "@tei/material";
import { ActivitiesLocationsSettingsService } from "../../@services/activities-locations-settings.service";
import { ActivitiesLocationsSettingsComponent } from "./activities-locations-setting.component";
import { RouteData } from "@tei/core";

const CORE_MODULES = [TeiTextTranslatePipeModule, TeiDateTranslateModule, TranslateModule];
const BASE_MODULES = [FormsModule, CommonModule, RouterModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatCheckboxModule, MatSortModule, MatCardModule, MatTableModule, MatInputModule,
  MatPaginatorModule, MatMenuModule, MatTooltipModule, MatTabsModule,];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, LoadingModule, UploadProfilePictureModule,QuickSearchFilterChipModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const ACTIVITIES_MODULES = [];
@NgModule({
  declarations: [ActivitiesLocationsSettingsComponent],
  imports: [
    ...CORE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES, ACTIVITIES_MODULES,
    RouterModule.forChild([
      { path: '', component: ActivitiesLocationsSettingsComponent },
      {
        path: 'manage-location/:id',
        loadChildren: () => import('../manage-locations-settings/manage-locations-settings.module').then(m => m.ManageLocationsSettingsModule),
        data: { RouteData: new RouteData({ Title: 'Manage Location' }) }
      },
    ]),
  ],
  exports: [RouterModule],
  providers: [{ provide: 'IActivitiesLocationsSettingsService', useClass: ActivitiesLocationsSettingsService }]

})
export class ActivitiesLocationsSettingsModule
{ }
