import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IActivitiesEventsSettingsService } from '../@intermediate/activities-events-settings/activities-events-settings.interface';
import { DeleteEventRequest, DeleteEventResponse } from '../@intermediate/activities-events-settings/models/delete-event.model';
import { GetGetEventPriceRequest, GetGetEventPriceResponse } from '../@intermediate/activities-events-settings/models/get-event-price.model';
import { GetActivitiesEventsSettingsRequest, GetActivitiesEventsSettingsResponse } from '../@intermediate/activities-events-settings/models/get-events.model';
import { PostActivityServicesResponse } from '../@intermediate/activity-services';

@Injectable({
  providedIn: 'root'
})

export class ActivitiesEventsSettingsService implements IActivitiesEventsSettingsService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/Booking/Activities/ActivityEvents";
  }

  Delete(request: DeleteEventRequest): Observable<DeleteEventResponse>
  {
    const apiUrl = this.serviceUrl + '/DeleteEvent';
    return this.http.delete<DeleteEventResponse>(apiUrl, { params: request as any });
  }

  Get(request: GetActivitiesEventsSettingsRequest): Observable<GetActivitiesEventsSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetEvents';
    return this.http.post<GetActivitiesEventsSettingsResponse>(apiUrl, request);
  }

  public Post(request: FormData): Observable<PostActivityServicesResponse>
  {
    const apiUrl = this.serviceUrl + '/PostEvent';
    return this.http.post<PostActivityServicesResponse>(apiUrl, request);
  }

  GetEventPrice(request: GetGetEventPriceRequest): Observable<GetGetEventPriceResponse>
  {
    const apiUrl = this.serviceUrl + '/GetEventPricie';
    return this.http.get<GetGetEventPriceResponse>(apiUrl, { params: request as any });
  }
}
