import { ManageServiceComponent } from './manage-service.component';

const CORE_MODULES = [TeiTextTranslatePipeModule, TeiDateTranslateModule, TranslateModule];
const BASE_MODULES = [FormsModule, CommonModule, RouterModule, ReactiveFormsModule];
