import { FormControl, FormGroup, Validators } from '@angular/forms';
import { GetManageServiceResponse } from './get-manage-service.model';

export class PostManageServiceRequest
{

}

export class PostManageServiceRequestForm extends FormGroup
{
  constructor()
  {
    super({
      ServiceName: new FormControl('', Validators.required),
      ActivityServiceID: new FormControl(1),
      ExternalCode: new FormControl('', Validators.required),
      ServiceDesc: new FormControl(''),
      Active: new FormControl(),
      Image: new FormControl(''),
      ImageUrl: new FormControl(''),
      Locations: new FormControl(),
      Categories: new FormControl(),
      Details: new FormControl(''),
      EarliestAvailableTime: new FormControl(),
      LatestAvailableTime: new FormControl(),
      BackOfficeItemID: new FormControl('', Validators.required),
      HideDurationSelection: new FormControl(false),
      HidePartySizeSelection: new FormControl(false),
      PermissionTable: new PermissionTableFormGroup(),
      Settings: new FormGroup({})
    });
  }

  public value: PostManageServiceRequest;

  public setValue(value: GetManageServiceResponse)
  {
    let commaSeparatedLocations: number[] = [];
    let commaSeparatedCategories: number[] = [];
    value.AllActivityLocations.forEach(al =>
    {
      let fg = new LocationFormGroup();

      fg.setValue(al);

      if (al.IsSelected)
      {
        commaSeparatedLocations.push(al.ActivitiesLocationID);
      }

    });

    value.AllActivityCategories.forEach(al =>
    {
      if (al.IsSelected)
      {
        commaSeparatedCategories.push(al.ActivityCategoryID);
      }
    });


    super.patchValue({
      ServiceName: value.ServiceName,
      ActivityServiceID: value.ActivitiesServiceID,
      ExternalCode: value.ExternalServiceID,
      BackOfficeItemID: value.BackOfficeItemID,
      ServiceDesc: value.ServiceDesc,
      Active: value.Active,
      Image: '',
      ImageUrl: value.ProfilePhotoURL,
      Locations: commaSeparatedLocations,
      Categories: commaSeparatedCategories,
      Details: value.Details || '',
      EarliestAvailableTime: new Date(value.EarliestTime),
      LatestAvailableTime: new Date(value.LatestTime),
      HideDurationSelection: value.HideDurationSelection,
      HidePartySizeSelection: value.HidePartySizeSelection,
      PermissionTable: value.PermissionTable
    });
  }

}

export class LocationFormGroup extends FormGroup
{
  constructor()
  {
    super({
      LocationName: new FormControl('', Validators.required),
      ActivitiesLocationID: new FormControl('', Validators.required),
      IsSelected: new FormControl(false)
    });
  }
}

export class PermissionTableFormGroup extends FormGroup
{
  constructor()
  {
    super({
      // AllowLotteryRequest: new SettingFormGroup("AllowLotteryRequest"),
      ContactLookupTemplateUID: new SettingFormGroup("ContactLookupTemplateUID"),
      AutomatedNotificationID: new SettingFormGroup("AutomatedNotificationID"),
      FieldGroupID: new SettingFormGroup("FieldGroupID"),
    });
  }
}

export class SettingFormGroup extends FormGroup
{
  constructor(name: string)
  {
    super({
      Id: new FormControl(),
      ColumnKeyNames: new FormControl(["Administrator", "Staff", "Member", "Public"]),
      Administrator: new FormControl(Validators.required),
      Staff: new FormControl(Validators.required),
      Member: new FormControl(Validators.required),
      Public: new FormControl(Validators.required),
    });
  }
}


export class PostManageServiceResponse
{
  InsertedServiceID: number;
  UpdatedServiceID: number;
}
