import { Component, Inject, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatRadioChange } from '@angular/material/radio';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import moment from 'moment';
import { finalize } from 'rxjs';
import { GetPricingResponse } from '../../@intermediate/activity-services/models/get-pricing.model';
import { IActivitiesServicesService } from '../../@intermediate/booking-activities/booking-activities.interface';
import { ActivitiesServicesDynamicFormModel, ActivitiesServicesFormModel, AvailableEmployee, AvailableService, AvailableSlot } from '../../@intermediate/booking-activities/models/get-booking-activities.model';
@Component({
  selector: 'tei-booking-activities-services-dialog',
  templateUrl: './activities-services-dialog.component.html',
  styleUrls: ['./activities-services-dialog.component.scss']
})
export class ActivitiesServicesDialogComponent implements OnInit
{
  pricing?: GetPricingResponse;
  selectedEmployee?: AvailableEmployee;
  step = 0;
  shouldShowBackButton = true;
  constructor(
    public dialogRef: MatDialogRef<ActivitiesServicesDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {
      buttonLabel: string;
      service: AvailableService;
      form;
      timeSlot: AvailableSlot;
      formModel: ActivitiesServicesFormModel | any;
      formModelDynamic: ActivitiesServicesDynamicFormModel[];
      locationID: number;
    },
    @Inject('IActivitiesServicesService') private activitiesServicesService: IActivitiesServicesService,
    private textTranslateService: TranslateService,
    private manageLoadingService: ManageLoadingService
  ) { }

  today: number = Date.now();
  ngOnInit(): void
  {
    const hasEmployee = !!this.data.formModel.Employee.Items?.length;
    const isSingleExternalEmployee = this.data.timeSlot.ExternalEmployee.length === 1;
    const hasValidEmployee = this.data.form.Employee !== 0 || isSingleExternalEmployee;

    this.selectedEmployee = hasEmployee && hasValidEmployee ? this.data.timeSlot.ExternalEmployee[0] : null;

    if (!hasEmployee || hasValidEmployee)
    {
      this.step++;
      this.shouldShowBackButton = false;
      this.getPricing();
    }
  }

  getPricing(): void
  {
    this.manageLoadingService.startLoading();
    this.activitiesServicesService.GetPricing({
      GroupSize: this.data.form.PartySize,
      ActivitiesLocationID: this.data.locationID,
      ActivitiesServiceID: this.data.service.ActivitiesServiceID,
      ExternalEmployeeID: this.selectedEmployee?.ExternalEmployeeID != null
        ? +this.selectedEmployee.ExternalEmployeeID
        : null,
      ExternalResourceUnitIDList: this.data.timeSlot.ExternalResourceUnitIDList.filter(item => typeof item === 'string') ?? [],
      StartTime: this.data.timeSlot.StartTime,
      ServiceLength: this.data.form.Duration
    }).pipe(finalize(() => { this.manageLoadingService.finishLoading(); })).subscribe(response =>
    {
      this.pricing = response;
    });
  }

  employeeChanged(paylaod: MatRadioChange): void
  {
    this.selectedEmployee = paylaod.value;
  }

  getNameByID(id: number, index: number): string
  {
    if (!this.data.formModelDynamic || !this.data.formModelDynamic[index])
    {
      return;
    }
    return this.data.formModelDynamic[index].Items?.filter(item => item.value === id)[0].label;
  }

  closeDialog(): void
  {
    this.dialogRef.close();
  }

  stepBack(): void
  {
    this.step--;
    this.pricing = undefined;
    this.selectedEmployee = undefined;
  }

  submitDialog(): void
  {
    if (this.step === 0)
    {
      this.step++;
      this.getPricing();
      return;
    }
    this.dialogRef.close({ courses: this.data, selectedEmployee: this.selectedEmployee, pricing: this.pricing });
  }

  getMoment(time: string): string
  {
    return moment(time).format('h:mmA');
  }
}
