import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { UntypedFormControl } from '@angular/forms';
import { ContactSelectionWrapperComponent, WrapperSubmitCallbackArgs } from '@tei/common-contact-lookup-shared';
import { GetPricingResponse } from '../../@intermediate/activity-services/models/get-pricing.model';
import { ActivitiesEventBookingData, ActivitiesServicesDynamicFormModel, ActivitiesServicesFormModel, AvailableEvent, AvailableService, AvailableSlot, GetContactLookupTemplateResponse, GetServiceDetailsResponse } from '../../@intermediate/booking-activities/models/get-booking-activities.model';
import { PostActivitiesServicesRequestForm } from '../../@intermediate/booking-activities/models/post-booking-activities.model';

@Component({
  selector: 'tei-activities-group-details',
  templateUrl: './activities-group-details.component.html',
  styleUrls: ['./activities-group-details.component.scss']
})
export class ActivitiesGroupDetailsComponent
{
  constructor() { }
  @ViewChild('contactsList') contactsList: ContactSelectionWrapperComponent;
  @Input() form: PostActivitiesServicesRequestForm;
  @Input() availableEvent: AvailableEvent;
  @Input() availableService: AvailableService;
  @Input() formModelDynamic: ActivitiesServicesDynamicFormModel[];
  @Input() formData: GetServiceDetailsResponse;
  @Input() activitiesEventBookingData: ActivitiesEventBookingData;
  @Input() pricing: GetPricingResponse;
  contactLookupHandlar: boolean;
  isLoading: boolean = false;
  _formModel: ActivitiesServicesFormModel;
  get formModel(): ActivitiesServicesFormModel
  {
    return this._formModel;
  }
  @Input() set formModel(value: ActivitiesServicesFormModel)
  {
    this._formModel = value;
    this.selectedEmployee = this.formModel?.Employee?.Items?.filter(elem => this.form.value.Employee === elem.value)?.shift()?.label;
  }
  @Input() contactLookupConfig: GetContactLookupTemplateResponse = null;
  @Input() availableSlot: AvailableSlot;
  @Output() onSubmitContactLookup: EventEmitter<any> = new EventEmitter();
  selectedEmployee: string;
  showMore: boolean = false;
  getFormControlByName(name: string): UntypedFormControl
  {
    return this.form.get(name) as UntypedFormControl;
  }

  getNameByID(id: number, index: number): string
  {
    if (!this.formModelDynamic || !this.formModelDynamic[index])
    {
      return;
    }
    return this.formModelDynamic[index].Items?.filter(item => item.value === id)[0].label;
  }

  toggleShowMore(): void
  {
    this.showMore = !this.showMore;
  }
  contactLookupCallback($event: WrapperSubmitCallbackArgs): void
  {
    this.onSubmitContactLookup.emit($event);
  }
  contactLookupContactChanged($event: WrapperSubmitCallbackArgs): void
  {
    this.isLoading = false;
  }
  get contactsListFormArray()
  {
    return this.form.get('ContactsList');
  }

  isDisabled(): boolean
  {
    return ((this.contactsListFormArray['controls'].some(form => form.invalid)) ||
      this.contactsList?.form?.status === "INVALID" ||
      this.isLoading);
  }

  activitiesSubmit(): void
  {
    this.isLoading = true;
    //this.isDisabled();
    this.contactsList.submit();
  }

}
