import { FormControl, FormGroup, Validators } from '@angular/forms';
import { GetManageEventResponse } from './get-manage-event.model';

export class PostManageEventRequest
{

}

export class PostManageEventRequestForm extends FormGroup
{
  constructor()
  {
    super({
      ActivityEventName: new FormControl('', Validators.required),
      ActivityEventID: new FormControl(''),
      ExternalCode: new FormControl('', Validators.required),
      ActivityEventDesc: new FormControl(''),
      EventDetails: new FormControl(''),
      Active: new FormControl(false),
      Image: new FormControl(''),
      ProfilePhotoURL: new FormControl(''),
      Locations: new FormControl(),
      Categories: new FormControl(),
      Details: new FormControl(''),
      BackOfficeItemID: new FormControl('', Validators.required),
      PermissionTable: new PermissionTableFormGroup(),
      Settings: new FormGroup({})
    });
  }

  public value: PostManageEventRequest;

  public setValue(value: GetManageEventResponse)
  {
    let commaSeparatedLocations: number[] = [];
    let commaSeparatedCategories: number[] = [];
    value.AllActivityLocations.forEach(al =>
    {
      let fg = new LocationFormGroup();
      fg.setValue(al);

      if (al.IsSelected)
      {
        commaSeparatedLocations.push(al.ActivityLocationID);
      }
    });

    value.AllActivityCategories.forEach(al =>
    {
      if (al.IsSelected)
      {
        commaSeparatedCategories.push(al.ActivityCategoryID);
      }
    });

    super.patchValue({
      ActivityEventName: value.EventName,
      ActivityEventID: value.ActivityEventID,
      ExternalCode: value.ExternalCode,
      ActivityEventDesc: value.EventDesc,
      Active: value.Active,
      BackOfficeItemID: value.BackOfficeItemID,
      Image: '',
      ProfilePhotoURL: value.ProfilePhotoURL,
      Locations: commaSeparatedLocations,
      Categories: commaSeparatedCategories,
      Details: value.Details,
      PermissionTable: value.PermissionTable
    });
  }

}

export class LocationFormGroup extends FormGroup
{
  constructor()
  {
    super({
      LocationName: new FormControl('', Validators.required),
      ActivityLocationID: new FormControl('', Validators.required),
      IsSelected: new FormControl(false),
      Active: new FormControl()
    });
  }
}

export class PermissionTableFormGroup extends FormGroup
{
  constructor()
  {
    super({
      ContactLookupTemplateUID: new SettingFormGroup("ContactLookupTemplateUID"),
      AutomatedNotificationID: new SettingFormGroup("AutomatedNotificationID"),
      FieldGroupID: new SettingFormGroup("FieldGroupID"),
    });
  }
}

export class SettingFormGroup extends FormGroup
{
  constructor(name: string)
  {
    super({
      Id: new FormControl(),
      ColumnKeyNames: new FormControl(["Administrator", "Staff", "Member", "Public"]),
      Administrator: new FormControl(Validators.required),
      Staff: new FormControl(Validators.required),
      Member: new FormControl(Validators.required),
      Public: new FormControl(Validators.required),
    });
  }
}


export class PostManageEventResponse
{
  EventID: number;
}
