import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IAppointmentsService } from '../@intermediate/appointments/appointments.interface';
import { GetAppointmentBookingDetailRequest, GetAppointmentBookingDetailResponse } from '../@intermediate/appointments/models/get-appointment-booking-detail.model';
import { PostAppointmentRequest, PostAppointmentResponse } from '../@intermediate/appointments/models/post-apponiment-services.model';
import { PostCancelAppointmentRequest, PostCancelAppointmentResponse } from '../@intermediate/appointments/models/post-cancel-appointment.model';
import { GetActivitiesAppointmentRequest, GetActivitiesAppointmentResponse } from '../@intermediate/booking-activities/models/get-booking-activities.model';

@Injectable({
  providedIn: 'root'
})

export class AppointmentsService implements IAppointmentsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/Booking/Activities/Appointments";
  }

  BookServiceAppointment(request: PostAppointmentRequest): Observable<PostAppointmentResponse>
  {
    const apiUrl = this.serviceUrl + '/BookServiceAppointment';
    return this.http.post<PostAppointmentResponse>(apiUrl, request);
  }

  BookEventAppointment(request: PostAppointmentRequest): Observable<PostAppointmentResponse>
  {
    const apiUrl = this.serviceUrl + '/BookEventAppointment';
    return this.http.post<PostAppointmentResponse>(apiUrl, request);
  }

  GetAppointMent(request: GetActivitiesAppointmentRequest): Observable<GetActivitiesAppointmentResponse>
  {
    const apiUrl = this.serviceUrl + '/GetActivityAppointments';
    return this.http.get<GetActivitiesAppointmentResponse>(apiUrl, { params: request as any });
  }

  CancelAppointment(request: PostCancelAppointmentRequest): Observable<PostCancelAppointmentResponse>
  {
    const apiUrl = this.serviceUrl + '/CancelAppointment';
    return this.http.post<PostCancelAppointmentResponse>(apiUrl, request);
  }

  GetBookingDetail(request: GetAppointmentBookingDetailRequest): Observable<GetAppointmentBookingDetailResponse>
  {
    const apiUrl = this.serviceUrl + '/GetBookingDetail';
    return this.http.get<GetAppointmentBookingDetailResponse>(apiUrl, { params: request as any });
  }
}
