import { Component, Input, OnInit, SecurityContext } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ResponsiveService } from '@tei/core';
import { AvailableService } from '../../@intermediate/booking-activities/models/get-booking-activities.model';

@Component({
  selector: 'tei-activities-service-expansion-panel',
  templateUrl: './activities-service-expansion-panel.component.html',
  styleUrls: ['./activities-service-expansion-panel.component.scss']
})
export class ActivitiesServiceExpantionPanelComponent implements OnInit
{
  @Input() service: AvailableService;
  @Input() fullHeight: boolean = true;
  isSmallScreen = false;
  constructor(private responsiveService: ResponsiveService, private domSanitizer: DomSanitizer) { }

  ngOnInit(): void
  {
    this.getScreenSize();
  }

  private getScreenSize(): void
  {
    const screenSize = this.responsiveService.breakPoint;
    this.isSmallScreen = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
  }

  sanitize(details: string)
  {
    return this.domSanitizer.sanitize(SecurityContext.NONE, details);
  }
}
