import { Component, Inject, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { firstValueFrom } from 'rxjs';
import { IActivitiesEventsSettingsService } from '../../@intermediate/activities-events-settings/activities-events-settings.interface';
import { GetGetEventPriceRequest } from '../../@intermediate/activities-events-settings/models/get-event-price.model';
import { ActivitiesEventDialogModel, AvailableEvent, Occurrence, OccurrenceDetail } from '../../@intermediate/booking-activities/models/get-booking-activities.model';

@Component({
  selector: 'tei-booking-activities-event-dialog',
  templateUrl: './activities-event-dialog.component.html',
  styleUrls: ['./activities-event-dialog.component.scss']
})
export class ActivitiesEventDialogComponent implements OnInit
{

  constructor(
    public dialogRef: MatDialogRef<ActivitiesEventDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {
      buttonLabel: string;
      event: AvailableEvent;
      eventOccurrence: Occurrence;
      location: string;
      locationId: number;
      occurrenceDetails: OccurrenceDetail[];
    },
    @Inject("IActivitiesEventsSettingsService") private activitiesEventsSettingsService: IActivitiesEventsSettingsService,
    private textTranslateService: TranslateService,
  ) { }
  numberOfRegistrant = 1;
  subTotal: number;
  tax: number;
  total: number;
  showMore: boolean = false;
  isLoading: boolean = false;

  async ngOnInit(): Promise<void>
  {
    await this.packageSelect(1);
  }

  closeDialog()
  {
    this.dialogRef.close();
  }
  submitDialog()
  {

    this.dialogRef.close({
      numberOfRegistrant: this.numberOfRegistrant,
      subTotal: this.subTotal,
      tax: this.tax,
      total: this.total
    } as ActivitiesEventDialogModel);

  }
  async packageSelect(quantity: number)
  {
    try
    {
      this.isLoading = true;

      const request: GetGetEventPriceRequest = {
        OccurenceID: this.data.eventOccurrence.EventOccurrenceID,
        GroupSize: quantity,
        ActivitiesEventID: this.data.event.ActivitiesEventID,
        ActivitiesLocationID: this.data.locationId
      };

      const response = await firstValueFrom(this.activitiesEventsSettingsService.GetEventPrice(request));

      this.subTotal = response.Price;
      this.tax = response.Tax;
      this.total = response.Total;
      this.numberOfRegistrant = quantity;
    } catch (error)
    {

    } finally
    {
      this.isLoading = false;
    }
  }

  toggleShowMore()
  {
    this.showMore = !this.showMore;
  }
}
