import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IActivityCategoryService } from '../@intermediate/activity-category/activity-category.interface';
import { GetActivityCategoryRequest, GetActivityCategoryResponse } from '../@intermediate/activity-category/models/get-activity-category.model';

@Injectable({
  providedIn: 'root'
})

export class ActivityCategoryService implements IActivityCategoryService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/Booking/Activities/ActivityCategory";
  }

  Get(request: GetActivityCategoryRequest): Observable<GetActivityCategoryResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetActivityCategoryResponse>(apiUrl, { params: request as any });
  }
}
