import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { ActivitiesEventsSettingsComponent } from './activities-events-settings.component';

import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTabsModule as MatTabsModule } from '@angular/material/legacy-tabs';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { MatSortModule } from '@angular/material/sort';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule, QuickSearchFilterChipModule, UploadProfilePictureModule } from '@tei/material';
import { AvatarModule } from 'ngx-avatar';
import { ActivitiesEventsSettingsService } from '../../@services/activities-events-settings.service';
import { ActivityEventsFilterModule } from '../../components/activity-events-filter/activity-events-filter.module';

const CORE_MODULES = [TeiTextTranslatePipeModule, TeiDateTranslateModule, TranslateModule, ActivityEventsFilterModule];
const BASE_MODULES = [FormsModule, CommonModule, RouterModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatCheckboxModule, MatSortModule, MatCardModule, MatTableModule, MatInputModule,
  MatPaginatorModule, MatMenuModule, MatTooltipModule, MatTabsModule,];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, LoadingModule, AvatarModule, UploadProfilePictureModule, QuickSearchFilterChipModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [
    ActivitiesEventsSettingsComponent
  ],
  imports: [
    CommonModule,
    ...CORE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES,
    RouterModule.forChild([
      { path: '', component: ActivitiesEventsSettingsComponent },
      {
        path: 'manage-event',
        loadChildren: () => import('../manage-event/manage-event.module').then(m => m.ManageEventModule)
      },
      {
        path: 'manage-event/:id',
        loadChildren: () => import('../manage-event/manage-event.module').then(m => m.ManageEventModule)
      }
    ]),
  ],
  exports: [RouterModule],
  providers: [{ provide: 'IActivitiesEventsSettingsService', useClass: ActivitiesEventsSettingsService }]
})
export class ActivitiesEventsSettingsModule { }
