import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { RouterModule } from '@angular/router';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, SnackBarModule } from '@tei/material';
import { AppointmentsService } from '../../@services/appointments.service';
import { ConfirmationComponent } from './confirmation.component';

const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiDateTranslateModule, SnackBarModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatIconModule, MatExpansionModule, MatDividerModule, MatCardModule, MatChipsModule, MatButtonModule];
const TEI_MATERIAL_MODULES = [TeiTextTranslatePipeModule, BreadCrumbModule];

@NgModule({
  declarations: [
    ConfirmationComponent
  ],
  imports: [
    RouterModule.forChild([
      { path: '', component: ConfirmationComponent },
    ]),
    CommonModule,
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  providers: [
    { provide: 'IAppointmentsService', useClass: AppointmentsService }
  ]
})
export class ConfirmationModule { }
