import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, ImageUploaderComponent, ImageUploaderModel, SnackBarService } from '@tei/material';
import { base64ToFile } from 'ngx-image-cropper';
import { Subscription } from 'rxjs';
import { IActivitiesLocationsSettingsService } from '../../@intermediate/activities-locations-settings/activities-locations-settings.interface';
import { ActivitiesLocation, ActivitiesSortItem, GetActivitiesLocationsSettingsRequest } from '../../@intermediate/activities-locations-settings/models/get-locations.model';
import { PostLocationRequest } from '../../@intermediate/activities-locations-settings/models/post-location.model';

@Component({
  selector: 'tei-booking-activities-manage-locations-settings',
  templateUrl: './manage-locations-settings.component.html',
  styleUrls: ['./manage-locations-settings.component.scss']
})
export class ManageLocationsSettingsComponent implements OnInit
{
  defaultPicture: string = '/assets/images/tei-logo.png';
  ProfilePhotoUrl = this.defaultPicture;
  ImageName: string;
  locationID: string = null;
  isLoading = false;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.ACTIVITIES"),
      relativeUrl: '../../../',
    },
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.LOCATIONS"),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.MANAGE_LOCATIONS"),
      relativeUrl: ''
    },
  ];
  model: ActivitiesLocation;
  routeSubscription: Subscription;
  file: Blob;
  editMode = false;

  form = new FormGroup({
    LocationName: new FormControl('', [Validators.required]),
    ExternalLocationID: new FormControl('', [Validators.required]),
    LocationDescription: new FormControl(''),
    Active: new FormControl(false),
    HideImagesOnServicesPage: new FormControl(false),
    ServicesPerPage: new FormControl(20,[Validators.required, Validators.pattern(/^\d+$/), Validators.min(1),Validators.max(100)])
  });

  sortList: ActivitiesSortItem[] = [];

  constructor(
    @Inject("IActivitiesLocationsSettingsService") private locationsSettingsService: IActivitiesLocationsSettingsService,
    public dialog: MatDialog, 
    private snackBar: SnackBarService,
    private activatedRoute: ActivatedRoute,
    private translate: TranslateService,
    private router: Router,
    )
  {
  }

  ngOnInit()
  {
    this.routeSubscription = this.activatedRoute.params.subscribe(params =>
    {
      this.locationID = params['id'];
      if (this.locationID != 'null')
      {
        this.editMode = true;
        this.loadData();
      }
    });

  }
  loadData(): void
  {
    this.isLoading = true;
    let request: GetActivitiesLocationsSettingsRequest = {
      ActivitiesLocationID: this.editMode ? this.locationID : null
    };
    this.locationsSettingsService.Get(request).subscribe(
      (result) =>
      {
        this.isLoading = false;
        this.form.setValue({
          LocationName: result.Locations[0].LocationName,
          ExternalLocationID: result.Locations[0].ExternalLocationID,
          LocationDescription: result.Locations[0].LocationDescription,
          Active: result.Locations[0].Active,
          HideImagesOnServicesPage: result.Locations[0].HideServiceImages,
          ServicesPerPage: result.Locations[0].ServicePerPage,
        });
        this.sortList = result.Locations[0].Activities;
        if (result.Locations[0].ProfilePhotoUrl)
          this.ProfilePhotoUrl = result.Locations[0].ProfilePhotoUrl;
      },
      (err) =>
      {
        this.isLoading = false;
      });

  }

  onSave()
  {
    this.isLoading = true;
    const request = new PostLocationRequest();
    request.LocationID = this.editMode ? this.locationID : null;
    request.LocationName = this.form.value.LocationName;
    request.LocationDescription = this.form.value.LocationDescription;
    request.ExternalLocationID = this.form.value.ExternalLocationID;
    request.Active = this.form.value.Active;
    request.ProfilePhotoUrl = this.file === undefined ? this.ProfilePhotoUrl : null;
    request.ServicePerPage = this.form.value.ServicesPerPage;
    request.HideServiceImages = this.form.value.HideImagesOnServicesPage;
    request.Image = this.file;
    request.Activities = this.sortList.map((v, i) => ({...v, SortOrder: i}));

    this.locationsSettingsService.Post(this.convertBodyRequestToFormData(request)).subscribe(
      (response) =>
      {
        this.isLoading = false;
        if (!this.editMode)
        {
          this.snackBar.showError('Location Created successfully.');
          this.router.navigate(['../', response.LocationID], { relativeTo: this.activatedRoute });
        }
        else
        {
          this.snackBar.showError('Saved successfully.');
        }
      },
      (err) =>
      {
        this.isLoading = false;
      });
  }

  convertBodyRequestToFormData(bodyRequest: { [key in string | number]: any; } = {}): FormData
  {
    const formData = new FormData();
    Object.keys(bodyRequest).forEach(key =>
    {

      if (bodyRequest[key] instanceof Blob)
      {
        const fileExtension = bodyRequest[key]?.type.split('/')?.[1] ?? 'jpg';
        const fileName = key + "." + (fileExtension === 'x-icon' ? 'ico' : fileExtension);
        formData.append(key, bodyRequest[key], fileName);
      }
      else if (bodyRequest[key])
      {
        formData.append(key, key === "Activities" ? JSON.stringify(bodyRequest[key]) : bodyRequest[key]);
      }
    });
    return formData;

  }
  openDialog()
  {
    const imageFormats = '.jpg,.jpeg,.jpe,.jif,.jfif,.jfi,.png,.gif,.bmp,.dib,.tiff,.tif,.heif,.heic';
    const permittedFileTypes = ['JPG', 'JPEG', 'JPE', 'JFIF', 'PNG', 'GIF', 'BMP', 'TIFF', 'HEIC'];   
    const data: ImageUploaderModel = {
      round: false,
      maintainAspectRatio: false,
      aspectRatio: null,
      acceptFormat: imageFormats,
      permittedFileTypes: permittedFileTypes,
      maxSize: 500,
      hideRemoveButton: true,
      imageServerUrl: this.ProfilePhotoUrl,
    };

    const imageUploaderDialog = this.dialog.open(ImageUploaderComponent, {
      width: '400px',
      panelClass: ['o-dialog-md-fullscreen'],
      data,
    });

    imageUploaderDialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.ProfilePhotoUrl = result.croppedImage;
        this.file = base64ToFile(this.ProfilePhotoUrl);
      }
    });
  }

  drop(event: CdkDragDrop<ActivitiesSortItem[]>) {
    moveItemInArray(this.sortList, event.previousIndex, event.currentIndex);
  }


}
