import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Occurrence } from '../../@intermediate/booking-activities/models/get-booking-activities.model';
@Component({
  selector: 'tei-activities-occurrences',
  templateUrl: './activities-available-occurences.component.html',
  styleUrls: ['./activities-available-occurences.component.scss']
})
export class ActivitiesAvailableOccurencesComponent
{
  constructor() { }

  @Input() eventOccurrences: Occurrence[];
  @Output() eventClick = new EventEmitter<Occurrence | null>(null);

  onEventClick(occurrence: Occurrence)
  {
    this.eventClick.emit(occurrence);
  }
}
