import { Pipe, PipeTransform } from '@angular/core';
import { AvailableActivity, GetActivitiesAvailableServicesRequest } from '../../@intermediate/booking-activities/models/get-booking-activities.model';

@Pipe({
  name: 'activityFilter',
  pure: false // Allows the pipe to update when filter changes
})
export class ActivityFilterPipe implements PipeTransform
{
  transform(activities: AvailableActivity[], filter: GetActivitiesAvailableServicesRequest): AvailableActivity[]
  {
    if (!activities || !filter)
    {
      return activities;
    }

    return activities.filter(activity =>
    {
      const matchesType = filter.ActivityType ? activity.Type === filter.ActivityType : true;
      const matchesSearch = filter.SearchString ? activity.ActivityName.toLowerCase().includes(filter.SearchString.toLowerCase()) : true;
      const matchesCategory = filter.CategoryIDs ? activity.SortOrder === filter.CategoryIDs : true;

      return matchesType && matchesSearch && matchesCategory;
    });
  }
}
