import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetManageServiceRequest, GetManageServiceResponse, IManageServiceService, PostManageServiceRequest, PostManageServiceResponse } from '../@intermediate/manage-service';

@Injectable({
  providedIn: 'root'
})

export class ManageServiceService implements IManageServiceService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl('Booking/Activities') + '/ActivityServices';
  }

  public Get(request: GetManageServiceRequest): Observable<GetManageServiceResponse>
  {
    const apiUrl = this.serviceUrl + '/ManageService';
    return this.http.get<GetManageServiceResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostManageServiceRequest): Observable<PostManageServiceResponse>
  {
    const apiUrl = this.serviceUrl + '';

    return this.http.post<PostManageServiceResponse>(apiUrl, request);
  }

}
