import { DesignationBasedSettingsModel } from "@tei/material-smart-components";

export class GetManageEventRequest
{
  EventID: number;
}

export class GetManageEventResponse
{
  ActivityEventID: string;
  PortalID: number;
  ExternalCode: string;
  EventName: string;
  EventDesc: string;
  ProfilePhotoURL: string;
  BackOfficeItemID: string;
  Active: boolean;
  PermissionTable: DesignationBasedSettingsModel;
  Details: string;
  AllActivityLocations: ActivityLocationBase[];
  AllActivityCategories: ActivityCategoryBase[];

  constructor()
  {
    this.AllActivityLocations = [];
    this.AllActivityCategories = [];
  }
}

export class ActivityLocationBase
{
  ActivityLocationID: number;
  LocationName: string;
  IsSelected: boolean;
  Active: boolean;
}

export class ActivityCategoryBase
{
  ActivityCategoryID: number;
  CategoryName: string;
  CategoryCode: string;
  IsSelected: boolean;
  Active: boolean;
}

export interface Option
{
  label: string;
  value: number;
}


export class Setting
{
  Options?: Option[];
  Id: any;
  SettingKeyName: string;
  SettingLabelName: string;
  ColumnKeyNames: string[];
  [key: string]: number | string | Option[] | string[];
}

export class PermissionTable
{
  Labels: string[];
  AllowBooking: Setting = new Setting();
  AutomatedNotificationID: Setting = new Setting();
  ContactLookupTemplateUID: Setting = new Setting();
  FieldGroupID: Setting = new Setting();
  PayStatus: Setting = new Setting();
}

export class ModelType
{
  PermissionTable: PermissionTable;
}

