import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { Component, ElementRef, Inject, OnInit, ViewChild } from '@angular/core';
import { UntypedFormArray, UntypedFormControl } from '@angular/forms';
import { MatLegacyAutocompleteSelectedEvent as MatAutocompleteSelectedEvent } from '@angular/material/legacy-autocomplete';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb, ImageUploaderComponent, ImageUploaderModel, SnackBarService } from '@tei/material';
import { DesignationBasedSettingsModel } from '@tei/material-smart-components';
import { Observable, Subject, catchError, concat, debounceTime, firstValueFrom, of, switchMap } from 'rxjs';
import { ActivityLocationBase, GetManageEventRequest, GetManageEventResponse, IManageEventService, PostManageEventRequestForm } from '../../@intermediate/manage-event';
import { GetPriceOverrideItemsRequest, RetailItem } from '../../@intermediate/retail-items/models/get-price-override-items.model';
import { IRetailItemsService } from '../../@intermediate/retail-items/retail-items.interface';
import { TeiChipsModel } from '../../components/tei-chips/tei-chips.component';

@Component({
  selector: 'tei-booking-activities-manage-event',
  templateUrl: './manage-event.component.html',
  styleUrls: ['./manage-event.component.scss']
})
export class ManageEventComponent implements OnInit
{
  settings: DesignationBasedSettingsModel = {};
  file: Blob;
  form: PostManageEventRequestForm;
  model: GetManageEventResponse;
  eventID: number;
  editMode: boolean;
  isLoading = false;
  defaultPicture: string = '/assets/images/tei-logo.png';
  selectedImage = this.defaultPicture;
  ImageName: string;
  @ViewChild('locationInput') locationInput: ElementRef<HTMLInputElement>;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.ACTIVITIES"),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.EVENTS"),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant("ACTIVITIES.SETTINGS.MANAGE_EVENT"),
      relativeUrl: ''
    }
  ];
  readonly separatorKeysCodes = [ENTER, COMMA] as const;
  ProfilePhotoUrl = this.defaultPicture;
  teiChipData: TeiChipsModel = {
    allChips: []
  };
  teiCategoriesChipData: TeiChipsModel = {
    allChips: []
  };
  priceOverrideItemArray$: Observable<RetailItem[]>;
  priceOverrideItemTypeaheadInput$ = new Subject<string>();
  retailItems: RetailItem[];
  isSmallScreen = false;

  constructor(
    public dialog: MatDialog,
    @Inject("IManageEventService") private manageEventService: IManageEventService,
    @Inject("IRetailItemsService") private retailItemsService: IRetailItemsService,
    private responsiveService: ResponsiveService,
    private translate: TranslateService,
    private activatedRoute: ActivatedRoute,
    private snackBar: SnackBarService,
    private router: Router
  ) { }

  ngOnInit(): void
  {
    this.ProfilePhotoUrl = this.defaultPicture;
    this.form = new PostManageEventRequestForm();
    this.activatedRoute.params.subscribe(params =>
    {
      this.eventID = params['id'];
      if (this.eventID > 0)
      {
        this.editMode = true;
      }
      this.loadData();
    });

    this.searchPriceOverrideItems();
    this.checkScreenSize();
  }

  openDialog(): void
  {
    const imageFormats = '.jpg,.jpeg,.jpe,.jif,.jfif,.jfi,.png,.gif,.bmp,.dib,.tiff,.tif,.heif,.heic';
    const permittedFileTypes = ['JPG', 'JPEG', 'JPE', 'JFIF', 'PNG', 'GIF', 'BMP', 'TIFF', 'HEIC'];
    const data: ImageUploaderModel = {
      round: false,
      maintainAspectRatio: false,
      aspectRatio: null,
      acceptFormat: imageFormats,
      permittedFileTypes: permittedFileTypes,
      maxSize: 500,
      hideRemoveButton: true,
      imageServerUrl: this.model.ProfilePhotoURL,
    };

    const imageUploaderDialog = this.dialog.open(ImageUploaderComponent, {
      width: '400px',
      panelClass: ['o-dialog-md-fullscreen'],
      data,
    });

    imageUploaderDialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.model.ProfilePhotoURL = result.croppedImage;
        this.ProfilePhotoUrl = this.model.ProfilePhotoURL;
        this.form.controls.Image.setValue(result.croppedImage);
      }
    });
  }

  async loadData(): Promise<void>
  {
    try
    {
      this.isLoading = true;

      const request: GetPriceOverrideItemsRequest = {

      };
      const response = await firstValueFrom(this.retailItemsService.PriceOverrideItems(request));

      this.retailItems = response.RetailItems;

      const manageEventRequest = new GetManageEventRequest();
      if (this.editMode)
      {
        manageEventRequest.EventID = this.eventID;
      }
      const manageEventResponse = await firstValueFrom(this.manageEventService.Get(manageEventRequest));

      this.model = manageEventResponse;
      this.settings = manageEventResponse.PermissionTable;

      if (!this.model.ProfilePhotoURL)
      {
        this.model.ProfilePhotoURL = '/assets/images/tei-logo.png';
      }

      this.teiChipData.allChips = this.model?.AllActivityLocations.sort((a, b) => a.LocationName.localeCompare(b.LocationName)).map(l => ({ id: l.ActivityLocationID, name: l.LocationName, active: l.Active }));
      this.teiCategoriesChipData.allChips = this.model?.AllActivityCategories.sort((a, b) => a.CategoryName.localeCompare(b.CategoryName)).map(l => ({ id: l.ActivityCategoryID, name: l.CategoryName, active: l.Active || true, code: l.CategoryCode }));
      this.form.setValue(manageEventResponse);
      this.onPriceOverrideItemsChange(manageEventResponse.BackOfficeItemID);
      this.isLoading = false;

    } catch (error)
    {
      this.isLoading = false;
    }
  }

  private checkScreenSize(): void
  {
    const screenSize = this.responsiveService.breakPoint;
    this.isSmallScreen = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
  }

  onPriceOverrideItemsChange(itemID: string): void
  {
    this.form.controls.BackOfficeItemID.setValue(itemID);
  };

  private searchPriceOverrideItems(): void
  {
    this.priceOverrideItemArray$ = concat(
      of([]), // default items
      this.priceOverrideItemTypeaheadInput$.pipe(
        debounceTime(300),
        switchMap((searchTerm = '') =>
          this.getPriceOverrideItems(searchTerm).pipe(
            catchError(() => of([])),
          ))
      )
    );
  }

  getPriceOverrideItems(term: string): Observable<RetailItem[]>
  {
    let items: RetailItem[] = [];

    if (term)
    {
      const lowerTerm = term.toLocaleLowerCase();
      items = this.retailItems.filter(x =>
      (x.Description?.toLocaleLowerCase().includes(lowerTerm) ||
        x.ItemID?.toLocaleLowerCase().includes(lowerTerm))
      );
    }

    return of(items);
  }

  get locations(): UntypedFormArray
  {
    return this.form.get("Locations") as UntypedFormArray;
  }

  removeLocation(i: number): void
  {
    this.locations.removeAt(i);
  }

  addLocation(event: MatAutocompleteSelectedEvent): void
  {
    this.locations.push(new UntypedFormControl(event.option.value));
    this.locationInput.nativeElement.value = '';
  }

  get selectedLocation(): ActivityLocationBase[]
  {
    return this.model?.AllActivityLocations.filter(location => this.locations.value.findIndex(locationID => locationID == location.ActivityLocationID) >= 0);
  }

  get notSelectedLocation()
  {
    return this.model?.AllActivityLocations.filter(location => this.locations.value.findIndex(locationID => locationID == location.ActivityLocationID) < 0);
  }

  onSave(): void
  {
    this.isLoading = true;
    const request: any = this.form.value;
    request.PermissionTable = this.convertNegativeOneToNull(request.Settings);
    delete request.Settings;
    this.manageEventService.Post(request).subscribe(
      (response) =>
      {
        this.isLoading = false;
        if (!this.editMode)
        {
          this.router.navigate(['../', response.EventID], { relativeTo: this.activatedRoute });
          this.snackBar.showSuccess(this.translate.instant('ACTIVITIES.MANAGE_EVENT.EVENT_CREATED_SUCCESSFULLY'), '', null, 'left');
        }
        else
        {
          this.snackBar.showSuccess(this.translate.instant('ACTIVITIES.MANAGE_EVENT.SAVED_SUCCESSFULLY'), '', null, 'left');
        }
      },
      (err) =>
      {
        this.isLoading = false;
      });
  }

  convertNegativeOneToNull(obj: Record<string, any>): Record<string, any>
  {
    // Check if obj is an object and not null
    if (typeof obj === "object" && obj !== null)
    {
      // Iterate over the keys in the object
      for (const key in obj)
      {
        if (obj.hasOwnProperty(key))
        {
          // If the property value is exactly "-1", set it to null
          if (obj[key] === "-1")
          {
            obj[key] = null;
          }
          // If the property is an object or an array, recurse into it
          else if (typeof obj[key] === "object")
          {
            this.convertNegativeOneToNull(obj[key]);
          }
        }
      }
    }
    return obj;
  }
}